import { __ } from '@wordpress/i18n';
import { TextControl, SelectControl, ToggleControl, BaseControl, __experimentalNumberControl as NumberControl, Button, __experimentalAlignmentMatrixControl as AlignmentMatrixControl, Dropdown, TextareaControl } from '@wordpress/components';

const { Shadow } = gspblib.collections;
const { GsbpFormLabel, OptionsGroupSet, StylesforBlock, BlpgeColorPicker } = gspblib.components;
import { rtransform, ttransform, svgtransform, svgdraw, staggertransform, svgmorph } from "../icons.js";

const CommonForm = (props) => {
    const {
        attributes: {
            animation_type,
            x,
            y,
            z,
            xo,
            yo,
            r,
            rx,
            ry,
            s,
            sx,
            sy,
            o,
            width,
            height,
            background,
            origin,
            custom_origin,
            strandom,
            stchild,
            text,
            stagger,
            stdelay,
            path,
            path_align,
            path_orient,
            path_align_x,
            path_align_y,
            path_start,
            path_end,
            morphend,
            morphstart,
            morphorigin,
            id
        },
        setAttributes,
    } = props;

    function animationReset() {
        setAttributes(
            {
                x: '',
                y: '',
                z: '',
                xo: '',
                yo: '',
                s: '',
                sx: '',
                sy: '',
                r: '',
                rx: '',
                ry: '',
                width: '',
                height: '',
                background: '',
                origin: '',
                shadow: '',
                o: null,
            }
        )
    }

    function animationEditorReset(animationref) {
        const gsapquick = animationref.querySelector('.gs-gsap-wrap');
        if (gsapquick) {
            let stgsap = ScrollTrigger.getById('gsinit'+id);
            if(stgsap) stgsap.kill();
            gsap.killTweensOf(gsapquick);
            if (typeof gsapsplitTextinit != 'undefined' && gsapsplitTextinit) {
                gsapsplitTextinit.revert();
            }
            gsapquick.style = '';
        }
    }

    function animationEditorInit(animationref) {
        const gsapquick = animationref.querySelector('.gs-gsap-wrap');
        if (gsapquick) {
            let ownerDocument = animationref.ownerDocument;
            let stgsap = ScrollTrigger.getById('gsinit'+id);
            if(stgsap) stgsap.kill();
            gsap.killTweensOf(gsapquick);
            if (typeof gsapsplitTextinit != 'undefined' && gsapsplitTextinit) {
                gsapsplitTextinit.revert();
            }
            if(!ownerDocument.body.classList.contains('editor-styles-wrapper')){
                GSinit(gsapquick, true, true, ownerDocument, id);
            }else{
                GSinit(gsapquick, true, false, ownerDocument, id);
            }
            
        }
    }

    return (
        <div className="gs-inspector-form-inspector">
            <div>{__('Select type', 'greenshiftgsap')}</div>
            <StylesforBlock columns={3} value={animation_type} onChange={val => setAttributes({ animation_type: val })}
                options={[
                    { value: 'regular', svg: rtransform, label: __('Transform', 'greenshiftgsap') },
                    { value: 'stagger_transformations', svg: staggertransform, label: __('Stagger', 'greenshiftgsap') },
                    { value: 'text_transformations', svg: ttransform, label: __('Inner Text', 'greenshiftgsap') },
                    { value: 'svg_motion_path', svg: svgtransform, label: __('SVG Path', 'greenshiftgsap') },
                    { value: 'svg_line_draw', svg: svgdraw, label: __('SVG draw', 'greenshiftgsap') },
                    { value: 'svg_morph', svg: svgmorph, label: __('Svg morph', 'greenshiftgsap') },
                ]}
            />
            <hr style={{ marginBottom: 18, marginTop: 10 }} />
            {animation_type === "svg_motion_path" && (
                <>
                    <GsbpFormLabel
                        title={__("Set path")}
                        clearFun={() => setAttributes({ path: null })}
                    />
                    <TextareaControl
                        value={path}
                        help={__("can be ID (place here id with #) or svg coordinates.", 'greenshiftgsap')}
                        onChange={(value) => setAttributes({ path: value })}
                    />
                    <span className="gspb_inspector_property-title">
                        {__("Align ID")}
                    </span>
                    <TextControl
                        value={path_align}
                        onChange={(value) => setAttributes({ path_align: value })}
                        help={__(
                            "By default, element is alighned by itself, but you can set id of path of another element to align.", 'greenshiftgsap'
                        )}
                    />
                    <div className="gspb_row">
                        <div className="gspb_row__col--10">
                            <span className="gspb_inspector_property-title">
                                {__("Orient along path", 'greenshiftgsap')}
                            </span>
                        </div>
                        <div className="gspb_row__col--2" style={{ marginTop: "5px" }}>
                            <ToggleControl
                                checked={path_orient}
                                onChange={(value) => setAttributes({ path_orient: value })}
                            />
                        </div>
                    </div>
                    <BaseControl className="gs-label-row">
                        <NumberControl
                            onChange={(value) => setAttributes({ path_align_x: parseFloat(value) })}
                            label={__('Align origin point X', 'greenshiftgsap')}
                            isDragEnabled
                            isShiftStepEnabled
                            shiftStep={1}
                            min={0}
                            step={0.1}
                            max={1}
                            value={path_align_x}
                        />
                    </BaseControl>
                    <BaseControl className="gs-label-row">
                        <NumberControl
                            onChange={(value) => setAttributes({ path_align_y: parseFloat(value) })}
                            label={__('Align origin point Y')}
                            isDragEnabled
                            isShiftStepEnabled
                            shiftStep={1}
                            min={0}
                            step={0.1}
                            max={1}
                            value={path_align_y}
                        />
                    </BaseControl>
                    <BaseControl className="gs-label-row">
                        <NumberControl
                            onChange={(value) => setAttributes({ path_start: parseFloat(value) })}
                            label={__('Start point')}
                            isDragEnabled
                            isShiftStepEnabled
                            shiftStep={1}
                            min={0}
                            step={0.1}
                            max={50}
                            value={path_start}
                        />
                    </BaseControl>
                    <BaseControl className="gs-label-row">
                        <NumberControl
                            onChange={(value) => setAttributes({ path_end: parseFloat(value) })}
                            label={__('End point')}
                            isDragEnabled
                            isShiftStepEnabled
                            shiftStep={1}
                            min={0}
                            step={0.1}
                            max={50}
                            value={path_end}
                        />
                    </BaseControl>
                    <hr style={{ marginBottom: 20, marginTop: 30 }} />
                </>
            )}
            {animation_type === "svg_morph" && (
                <>
                    <GsbpFormLabel
                        title={__("Set SVG path of end morph")}
                        clearFun={() => setAttributes({ morphend: null })}
                    />
                    <TextareaControl
                        value={morphend}
                        help={__("can be ID (place here id with #) or svg path coordinates.")}
                        onChange={(value) => setAttributes({ morphend: value })}
                    />
                    <GsbpFormLabel
                        title={__("Set ID of start shape")}
                        clearFun={() => setAttributes({ morphstart: null })}
                    />
                    <TextareaControl
                        value={morphstart}
                        help={__("By default, all svg which you place inside container will be used as start shape. In this field you can set ID of path which you want to use as start point")}
                        onChange={(value) => setAttributes({ morphstart: value })}
                    />
                    <span className="gspb_inspector_property-title">
                        {__("Custom origin")}
                    </span>
                    <TextControl
                        value={morphorigin}
                        onChange={() => setAttributes({ morphorigin: value })}
                        help={__(
                            "Default is: 50% 50%"
                        )}
                    />
                    <hr style={{ marginBottom: 20, marginTop: 20 }} />
                </>
            )}

            {animation_type === "svg_line_draw" && (
                <>
                    <div>{__('Please, add any svg with paths inside Container and enjoy animation')}</div>
                    <hr style={{ marginBottom: 20, marginTop: 20 }} />
                </>
            )}
            <>
                <div className="gspb_inspector_inputs-row__title gspb_row gspb_row--gutter-20">
                    <div class="gspb_row__col--4">
                        <BaseControl>
                            <NumberControl
                                onChange={(value) => setAttributes({ x: parseInt(value) })}
                                label={__('Shift X')}
                                isDragEnabled
                                isShiftStepEnabled
                                shiftStep={10}
                                step={1}
                                value={x}
                            />
                        </BaseControl>
                    </div>
                    <div class="gspb_row__col--4">
                        <BaseControl>
                            <NumberControl
                                onChange={(value) => setAttributes({ y: parseInt(value) })}
                                label={__('Y')}
                                isDragEnabled
                                isShiftStepEnabled
                                shiftStep={10}
                                step={1}
                                value={y}
                            />
                        </BaseControl>
                    </div>
                    <div class="gspb_row__col--4">
                        <BaseControl>
                            <NumberControl
                                onChange={(value) => setAttributes({ z: parseInt(value) })}
                                label={__('Z')}
                                isDragEnabled
                                isShiftStepEnabled
                                shiftStep={10}
                                step={1}
                                value={z}
                            />
                        </BaseControl>
                    </div>
                </div>

                <div className="gspb_inspector_inputs-row__title gspb_row gspb_row--gutter-20">
                    <div class="gspb_row__col--4">
                        <BaseControl>
                            <NumberControl
                                onChange={(value) => setAttributes({ r: parseInt(value) })}
                                label={__('Rotation')}
                                isDragEnabled
                                isShiftStepEnabled
                                shiftStep={10}
                                step={1}
                                value={r}
                            />
                        </BaseControl>
                    </div>
                    <div class="gspb_row__col--4">
                        <BaseControl>
                            <NumberControl
                                onChange={(value) => setAttributes({ rx: parseInt(value) })}
                                label={__('X')}
                                isDragEnabled
                                isShiftStepEnabled
                                shiftStep={10}
                                step={1}
                                value={rx}
                            />
                        </BaseControl>
                    </div>
                    <div class="gspb_row__col--4">
                        <BaseControl>
                            <NumberControl
                                onChange={(value) => setAttributes({ ry: parseInt(value) })}
                                label={__('Y')}
                                isDragEnabled
                                isShiftStepEnabled
                                shiftStep={10}
                                step={1}
                                value={ry}
                            />
                        </BaseControl>
                    </div>
                </div>
                <div className="gspb_inspector_inputs-row__title gspb_row gspb_row--gutter-20">
                    <div class="gspb_row__col--4">
                        <BaseControl>
                            <NumberControl
                                onChange={(value) => setAttributes({ s: parseFloat(value) })}
                                label={__('Scale')}
                                isDragEnabled
                                isShiftStepEnabled
                                shiftStep={1}
                                min={0}
                                max={30}
                                step={0.1}
                                value={s}
                            />
                        </BaseControl>
                    </div>
                    <div class="gspb_row__col--4">
                        <BaseControl>
                            <NumberControl
                                onChange={(value) => setAttributes({ sx: parseFloat(value) })}
                                label={__('X')}
                                isDragEnabled
                                isShiftStepEnabled
                                shiftStep={1}
                                min={0}
                                max={30}
                                step={0.1}
                                value={sx}
                            />
                        </BaseControl>
                    </div>
                    <div class="gspb_row__col--4">
                        <BaseControl>
                            <NumberControl
                                onChange={(value) => setAttributes({ sy: parseFloat(value) })}
                                label={__('Y')}
                                isDragEnabled
                                isShiftStepEnabled
                                shiftStep={1}
                                min={0}
                                max={30}
                                step={0.1}
                                value={sy}
                            />
                        </BaseControl>
                    </div>
                </div>
                <BaseControl className="gs-label-row">
                    <NumberControl
                        onChange={(value) => setAttributes({ o: parseInt(value) })}
                        label={__("Opacity")}
                        isDragEnabled
                        isShiftStepEnabled
                        shiftStep={10}
                        min={0}
                        step={1}
                        max={100}
                        value={o}
                    />
                </BaseControl>
                <BaseControl className="gs-label-row">
                    <NumberControl
                        onChange={(value) => setAttributes({ xo: parseInt(value) })}
                        label={__('Translate X (%)')}
                        isDragEnabled
                        isShiftStepEnabled
                        shiftStep={10}
                        step={1}
                        value={xo}
                    />
                </BaseControl>
                <BaseControl className="gs-label-row">
                    <NumberControl
                        onChange={(value) => setAttributes({ yo: parseInt(value) })}
                        label={__('Translate Y (%)')}
                        isDragEnabled
                        isShiftStepEnabled
                        shiftStep={10}
                        step={1}
                        value={yo}
                    />
                </BaseControl>
                {animation_type !== "text_transformations" && (
                    <>
                        <div className="gspb_row gspb_row--gutter-20">
                            <div className="gspb_row__col--8" style={{ padding: 0 }}>
                                {__("Width")}
                            </div>
                            <div className="gspb_row__col--4" style={{ padding: 0 }}>
                                <TextControl
                                    value={width}
                                    onChange={(value) => setAttributes({ width: value })}
                                />
                            </div>
                        </div>
                        <div className="gspb_row gspb_row--gutter-20">
                            <div className="gspb_row__col--8" style={{ padding: 0 }}>
                                {__("Height")}
                            </div>
                            <div className="gspb_row__col--4" style={{ padding: 0 }}>
                                <TextControl
                                    value={height}
                                    onChange={(value) => setAttributes({ heigth: value })}
                                />
                            </div>
                        </div>
                    </>
                )}
                {animation_type === "text_transformations" && (
                    <>
                        <hr style={{ marginBottom: 20, marginTop: 20 }} />
                        <div className="gspb_inspector_inputs-row__title gspb_row gspb_row--gutter-20" style={{ alignItems: "flex-start" }}>
                            <div class="gspb_row__col--8">
                                <span>{__("Break type for text")}</span>
                            </div>
                            <div class="gspb_row__col--4">
                                <SelectControl
                                    value={text}
                                    options={[
                                        { label: __("Lines"), value: "lines" },
                                        { label: __("Chars"), value: "chars" },
                                        { label: __("Words"), value: "words" },
                                    ]}
                                    onChange={(value) => setAttributes({ text: value })}
                                />
                            </div>
                        </div>
                        <BaseControl className="gs-label-row">
                            <NumberControl
                                onChange={(value) => setAttributes({ stdelay: parseFloat(value) })}
                                label={__('Stagger delay')}
                                isDragEnabled
                                isShiftStepEnabled
                                shiftStep={10}
                                min={0}
                                max={10}
                                step={0.1}
                                value={stdelay}
                            />
                        </BaseControl>
                        <div className="gspb_inspector_inputs-row__title gspb_row gspb_row--gutter-20">
                            <div className="gspb_row__col--10">
                                <span className="gspb_inspector_property-title">
                                    {__("Enable random order")}
                                </span>
                            </div>
                            <div className="gspb_row__col--2" style={{ marginTop: "5px" }}>
                                <ToggleControl
                                    checked={strandom}
                                    onChange={(value) => setAttributes({ strandom: value })}
                                />
                            </div>
                        </div>

                    </>
                )}
                {animation_type === "stagger_transformations" && (
                    <>
                        <OptionsGroupSet>
                            <div className="gspb_row__col--10">
                                <span className="gspb_inspector_property-title">
                                    {__("Enable stagger to child items")}
                                </span>
                            </div>
                            <div className="gspb_row__col--2" style={{ marginTop: "5px" }}>
                                <ToggleControl
                                    checked={stchild}
                                    onChange={(value) => setAttributes({ stchild: value })}
                                />
                            </div>
                            {!stchild && (
                                <>
                                    <GsbpFormLabel
                                        title={__("Or Set Stagger class")}
                                        clearFun={() => setAttributes({ stagger: null })}
                                    />
                                    <TextControl
                                        value={stagger}
                                        onChange={(value) => setAttributes({ stagger: value })}
                                        help={__(
                                            "You can set special class to trigger animation on all elements with this class with some delay between each item."
                                        )}
                                    />
                                </>
                            )}
                            <div className="gspb_row__col--10">
                                <span className="gspb_inspector_property-title">
                                    {__("Enable random order")}
                                </span>
                            </div>
                            <div className="gspb_row__col--2" style={{ marginTop: "5px" }}>
                                <ToggleControl
                                    checked={strandom}
                                    onChange={(value) => setAttributes({ strandom: value })}
                                />
                            </div>
                            <BaseControl className="gs-label-row">
                                <NumberControl
                                    onChange={(value) => setAttributes({ stdelay: parseFloat(value) })}
                                    label={__('Stagger delay')}
                                    isDragEnabled
                                    isShiftStepEnabled
                                    shiftStep={10}
                                    min={0}
                                    max={10}
                                    step={0.1}
                                    value={stdelay}
                                />
                            </BaseControl>
                        </OptionsGroupSet>
                    </>
                )}
                <hr style={{ marginBottom: 15, marginTop: 15 }} />
                <div className="gspb_row gspb_row--gutter-30">
                    <div className="gspb_row__col--6" style={{ padding: 0 }}>
                        <span className="gspb_inspector_property-title">
                            {__(
                                animation_type === "text_transformations"
                                    ? "Color"
                                    : "Background Color"
                            )}
                        </span>
                    </div>
                    <div className="gspb_row__col--6" style={{ textAlign: "right", justifyContent: "flex-end", padding: 0 }}>
                        <BlpgeColorPicker
                            color={background}
                            onChange={(value) =>
                                setAttributes({
                                    background: value.rgb ? `rgba(${value.rgb.r}, ${value.rgb.g}, ${value.rgb.b}, ${value.rgb.a})` : value,
                                })
                            }
                        />
                    </div>
                    <div
                        className="gspb_row__col--10"
                        style={{ display: "flex", justifyContent: "center", padding: 0 }}
                    >
                        {__("Shadow")}
                    </div>
                    <div className="gspb_row__col--2" style={{ padding: 0 }}>
                        <Dropdown
                            position="top left"
                            renderToggle={({ onToggle }) => (
                                <div>
                                    <button
                                        className="components-button components-icon-button components-toolbar__control"
                                        onClick={onToggle}
                                    >
                                        <i
                                            class="rhicon rhi-cog"
                                            style={{
                                                margin: "auto",
                                                fontSize: "20px",
                                            }}
                                        />
                                    </button>
                                </div>
                            )}
                            renderContent={({ onToggle }) => (
                                <div className="gspb_inspector_more_options">
                                    <Shadow
                                        attributeName="shadow"
                                        {...props}
                                        predefined={false}
                                    />
                                </div>
                            )}
                        />
                    </div>
                </div>
                <hr style={{ marginBottom: 15, marginTop: 15 }} />

                <div className="gspb_row gspb_row--gutter-30">
                    <div class="gspb_row__col--6" style={{ padding: "0 10px 0 0" }}>
                        {custom_origin ?
                            <TextControl
                                value={origin}
                                onChange={(value) => setAttributes({ origin: value })}
                                help={<OriginDocLink />}
                            />
                            :
                            <AlignmentMatrixControl
                                help={<OriginDocLink />}
                                label={__("Origin")}
                                value={origin}
                                onChange={(value) => setAttributes({ origin: value })}
                            />
                        }
                    </div>
                    <div class="gspb_row__col--6" style={{ padding: 0 }}>
                        <span style={{ display: "block" }} className="marginbottom10">
                            {__("Use custom transform origin")}
                        </span>
                        <ToggleControl
                            className="marginbottom10"
                            checked={custom_origin}
                            onChange={(value) => setAttributes({ custom_origin: value })}
                        />
                        <Button isSecondary isSmall onClick={() => {
                            animationReset();
                        }}>{__('Reset All values')}</Button>
                        <div style={{ marginBottom: 5 }}></div>
                        <Button isSecondary isSmall onClick={() => {
                            animationEditorReset(props.animationref);
                        }}>{__('Reset in Editor')}</Button>
                        <div style={{ marginBottom: 5 }}></div>
                        <Button isSecondary isSmall onClick={() => {
                            animationEditorInit(props.animationref);
                        }}>{__('Init in Editor')}</Button>
                    </div>
                </div>
            </>

        </div>
    );
};

export default CommonForm;

const OriginDocLink = () => {
    return (
        <>
            <a href="https://developer.mozilla.org/en-US/docs/Web/CSS/transform-origin" target="__blank">{__('Check documentation')}</a>
        </>
    )
}