const { __ } = wp.i18n;
import { SelectControl, TextControl, ToggleControl, BaseControl, __experimentalNumberControl as NumberControl } from '@wordpress/components';

const TriggerForm = (props) => {
	const {
		attributes: {
			triggertype,
			customtrigger,
			customobject,
			triggerstart,
			triggerend,
		},
		setAttributes,
	} = props;

	return (
		<div className="gs-inspector-form-inspector">
			<span className="gspb_inspector_property-title">
				{__("Trigger type")}
			</span>
			<SelectControl
				value={triggertype}
				options={[
					{ label: __("Scroll trigger"), value: "scroll" },
					{ label: __("On load"), value: "load" },
					{ label: __("On Hover"), value: "hover" },
					{ label: __("On Click"), value: "click" },
					{ label: __("Toggle Click"), value: "toggleclick" },
				]}
				onChange={(value) => setAttributes({ triggertype: value })}
				help={<TriggerDocLink />}
			/>
			<span className="gspb_inspector_property-title">
				{__("Object where to add animation")}
			</span>
			<TextControl
				value={customobject}
				onChange={(value) => setAttributes({ customobject: value })}
				help={__(
					"By default, will be applied to current object but you can apply to custom object. Use .class or #id of element OR div.class, img, button to search selector inside current container"
				)}
			/>
			{triggertype !== "load" && (
				<>

					<span className="gspb_inspector_property-title">
						{__("Selector for custom trigger")}
					</span>

					<TextControl
						value={customtrigger}
						onChange={(value) => setAttributes({ customtrigger: value })}
						help={__(
							"By default, trigger will be block itself, but you can set custom trigger. Use .class or #id of element OR div.class, img, button to search selector inside current container"
						)}
					/>
				</>
			)}
			{(triggertype !== "load" && triggertype !== "hover" && triggertype !== "click") && (
				<>
					<>
						<span className="gspb_inspector_property-title">
							{__("Trigger start")}
						</span>
						<TextControl
							value={triggerstart}
							onChange={(value) => setAttributes({ triggerstart: value })}
							help={__(
								"By default, trigger is set to top point of element, but you can change this. Example: top center"
							)}
						/>
					</>
					<>
						<span className="gspb_inspector_property-title">
							{__("Trigger end")}
						</span>
						<TextControl
							value={triggerend}
							onChange={(value) => setAttributes({ triggerend: value })}
							help={__(
								"By default, trigger scroll end is set to bottom point of element, but you can change this. Example: +=300 will set end of trigger as 300px after start"
							)}
						/>
					</>
				</>
			)}
		</div>
	);
};

export default TriggerForm;


const TriggerDocLink = () => {
	return (
		<>
			Check documentation for available values - <a href="https://greensock.com/docs/v3/Plugins/ScrollTrigger" target="__blank"> ScrollTrigger Docs </a>
		</>
	)
}