/**
 * Animation Container Block
 */

// Import Styles
import './editor.scss';

// Import wp dependencies
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
import { useEffect, useRef, useCallback } from '@wordpress/element';
const { select } = wp.data;
import {
    InnerBlocks
} from '@wordpress/block-editor';
import { seqicon } from './components/icons.js'

import attributes from './components/attributes';
import PinScroll from './components/PinScroll';
import Inspector from './components/inspector';


// Import gspb depenedencies
const { gspb_setBlockId } = gspblib.utilities;
const { BlockToolBar } = gspblib.components;

// Register Block
registerBlockType('greenshift-blocks/pinscroll', {
    title: __('Pin Scroll', 'greenshiftgsap'),
    description: __('Container to pin items while scroll'),
    category: 'Greenshiftpro',
    keywords: [__('gsap'), __('animation'), __('pin')],
    icon: seqicon,
    supports: {
        reusable: true,
        anchor: true,
        align: ['wide', 'full'],
    },

    // Define attributes
    attributes: attributes,

    edit(props) {

        // Set Unique Block ID
        gspb_setBlockId(props);

        const {
            attributes: {
                ease,
                delay,
                customtrigger,
                triggerstart,
                triggerend,
                triggerscrub,
                pinspace,
                pinReparent,
                anticipatePin,
            },
            clientId
        } = props;

        let hasChildBlocks = select('core/block-editor').getBlockOrder(clientId).length > 0;

        const AnimationRef = useRef();
        const isInitialMount = useRef(true);
        const isStillMounted = useRef();
        const debounce = useCallback(
            _.debounce((_atts) => {
                isStillMounted.current = true;
                if (isStillMounted.current && AnimationRef.current) {
                    let ownerDocument = AnimationRef.current.ownerDocument;
                    const gsapquick = AnimationRef.current.querySelector('.gs-gsap-wrap');
                    if (gsapquick) {
                        gsap.killTweensOf(gsapquick);
                        if (!ownerDocument.body.classList.contains('editor-styles-wrapper')) {
                            GSinit(gsapquick, false, true, ownerDocument);
                        } else {
                            GSinit(gsapquick, true, false, ownerDocument);
                        }
                    }
                }
                return () => { isStillMounted.current = false; };
            }, 600),
            []
        );
        useEffect(() => {
            if (isInitialMount.current) {
                isInitialMount.current = false;
                if (AnimationRef.current) {
                    const gsapquick = AnimationRef.current.querySelector('.gs-gsap-wrap');
                    let ownerDocument = AnimationRef.current.ownerDocument;
                    if (gsapquick) {
                        if (!ownerDocument.body.classList.contains('editor-styles-wrapper')) {
                            GSinit(gsapquick, false, true, ownerDocument);
                        } else {
                            GSinit(gsapquick, true, false, ownerDocument);
                        }
                    }
                }
            } else {
                debounce(attributes);
            }
        }, [
            ease,
            delay,
            customtrigger,
            triggerstart,
            triggerend,
            triggerscrub,
            pinspace,
            pinReparent,
            anticipatePin]);

        return (
            <>
                <Inspector animationref={AnimationRef.current} {...props} />
                <BlockToolBar {...props} />
                <div className="gs-animation" ref={AnimationRef}>
                    <PinScroll editor={true} {...props}>
                        <InnerBlocks
                            renderAppender={() => hasChildBlocks ? <InnerBlocks.DefaultBlockAppender /> : <InnerBlocks.ButtonBlockAppender />}
                        />
                    </PinScroll>
                </div>
            </>
        );
    },

    save(props) {
        return (
            <div className="gspb_pinscroll_wrap">
                <PinScroll editor={false} {...props}>
                    <InnerBlocks.Content />
                </PinScroll>
            </div>
        );
    },
});
