const {
    gspb_spacing_cssGen,
    gspb_responsive_cssGen,
    gspb_position_cssGen,
    gspb_csstransform_cssGen,
    gspb_Css_Final
} = gspblib.utilities;

export const AnimationContainer = (props) => {

    const {
        attributes: {
            id,
            inlineCssStyles,
            responsive,
            position,
            csstransform,
            spacing,
            delay,
            duration,
            triggertype,
            customtrigger,
            customobject,
            triggerstart,
            triggerend,
            triggeraction,
            flipscale,
            flipabsolute,
            flipstyle,
            ease
        },
        children,
        clientId
    } = props;

    let containerId = `gspb_gsapflip-${id}`;
    let css_selector_by_user = '#' + containerId;
    let final_css;

    // Responsive classes
    final_css = gspb_responsive_cssGen(
        responsive,
        css_selector_by_user,
        final_css,
    );

    // Position
    final_css = gspb_position_cssGen(
        position,
        'body.gspb-bodyfront ' + css_selector_by_user,
        final_css,
    );

    // Css transform
    final_css = gspb_csstransform_cssGen(
        csstransform,
        css_selector_by_user,
        final_css,
        css_selector_by_user
    );

    // Spacing
    final_css = gspb_spacing_cssGen(
        spacing,
        css_selector_by_user,
        final_css
    );

    // Get The Stored CSS
    gspb_Css_Final(id, final_css, props);
    let editor_css = final_css;

    // Position
    editor_css = gspb_position_cssGen(
        position,
        '#block-' + clientId,
        editor_css,
    );

    let customstyles = flipstyle.replace(/{GREENSHIFT}/g, css_selector_by_user);

    return (
        <div id={containerId}>

            <div
                id={id}
                className="gs-flip-wrap"
                data-delay={(delay && Number.isFinite(delay)) ? delay : null}
                data-duration={(duration && Number.isFinite(duration)) ? duration : null}
                data-triggertype={triggertype}
                data-customtrigger={customtrigger ? customtrigger : null}
                data-customobject={customobject ? customobject : null}
                data-triggerstart={triggerstart ? triggerstart : null}
                data-triggerend={triggerend ? triggerend : null}
                data-triggeraction={triggeraction ? triggeraction : null}
                data-flipscale={flipscale ? "yes" : null}
                data-absolute={flipabsolute ? "yes" : null}
                data-ease={ease ? ease : null}
            >
                {children}
            </div>

            {props.editor ? (
                <style
                    dangerouslySetInnerHTML={{
                        __html: editor_css,
                    }}
                />
            ) : null}
            {flipstyle ? (
                <style
                    dangerouslySetInnerHTML={{
                        __html: customstyles,
                    }}
                />


            ) : null}

        </div>
    );
};

export default AnimationContainer;
