/**
 * Set the block attributes
 * @type {Object}
 */
const { collectionsObjects } = gspblib.helpers;

export default {

    id: {
        type: 'string',
        default: null,
    },
    flexAlign: {
        type: 'string',
    },
    inlineCssStyles: {
        type: 'string',
    },
    ease: {
        type: 'string',
        default: 'none'
    },
    delay: {
        type: 'number',
        default: null
    },
    customtrigger: {
        type: 'string',
        default: null
    },
    triggerstart: {
        type: 'string',
        default: 'top 15%'
    },
    triggerend: {
        type: 'string',
        default: '+=350'
    },
    responsive: {
        type: 'object',
        default: collectionsObjects.responsive
    },
    position: {
        type: 'object',
        default: collectionsObjects.position,
    },
    csstransform: {
        type: 'object',
        default: collectionsObjects.csstransform,
    },
    border: {
		type: 'object',
		default: collectionsObjects.border,
	},
	background: {
		type: 'object',
		default: collectionsObjects.background,
	},
	spacing: {
		type: 'object',
		default: collectionsObjects.spacing,
	},

	shadow: {
		type: 'object',
		default: collectionsObjects.shadow,
	},
    // Interpolate animation by Scroll
    triggerscrub: {
        type: 'number',
        default: 1
    },

    //Enable overflow on pinned item
    pinspace: {
        type: 'boolean',
    },
    anticipatePin: {
        type: 'boolean',
    },
    pinReparent: {
        type: 'boolean',
    },
}