const { __ } = wp.i18n;
import { SelectControl, TextControl, ToggleControl, BaseControl, __experimentalNumberControl as NumberControl } from '@wordpress/components';

const TriggerForm = (props) => {
	const {
		attributes: {
			triggertype,
			customtrigger,
			customobject,
			triggerstart,
			triggerend,
			triggerscrub,
			pinned,
			pinspace,
			hoverpause

		},
		setAttributes,
	} = props;

	return (
		<div className="gs-inspector-form-inspector">
			<span className="gspb_inspector_property-title">
				{__("Trigger type")}
			</span>
			<SelectControl
				value={triggertype}
				options={[
					{ label: __("Scroll trigger"), value: "scroll" },
					{ label: __("On load"), value: "load" },
					{ label: __("On Hover"), value: "hover" },
					{ label: __("On Click"), value: "click" },
				]}
				onChange={(value) => setAttributes({ triggertype: value })}
				help={<TriggerDocLink />}
			/>
			<span className="gspb_inspector_property-title">
				{__("Css ID where to add animation")}
			</span>
			<TextControl
				value={customobject}
				onChange={(value) => setAttributes({ customobject: value })}
				help={__(
					"By default, will be applied to current object but you can apply to custom object"
				)}
			/>
			{triggertype !== "load" && (
				<>

					<span className="gspb_inspector_property-title">
						{__("Css ID of custom trigger")}
					</span>

					<TextControl
						value={customtrigger}
						onChange={(value) => setAttributes({ customtrigger: value })}
						help={__(
							"By default, animation will be triggered by itself. You can place here custom ID for element"
						)}
					/>
				</>
			)}
			{(triggertype !== "load" && triggertype !== "hover" && triggertype !== "click") && (
				<>
					<>
						<span className="gspb_inspector_property-title">
							{__("Trigger start")}
						</span>
						<TextControl
							value={triggerstart}
							onChange={(value) => setAttributes({ triggerstart: value })}
							help={__(
								"By default, trigger is set to top point of element, but you can change this. Example: top center"
							)}
						/>
					</>
					<>
						<span className="gspb_inspector_property-title">
							{__("Trigger end")}
						</span>
						<TextControl
							value={triggerend}
							onChange={(value) => setAttributes({ triggerend: value })}
							help={__(
								"By default, trigger scroll end is set to bottom point of element, but you can change this. Example: +=300 will set end of trigger as 300px after start"
							)}
						/>
					</>
					<>
						<span className="gspb_inspector_property-title">
							{__("Interpolate animation by Scroll")}
						</span>

						<NumberControl
							onChange={(value) => setAttributes({ triggerscrub: parseFloat(value) })}
							help={__(
								"By default, scroll will trigger full animation. If you want to play animation by scrolling, place here number of seconds for feedback. Better to check how it works on Frontend. Recommended value is 1."
							)}
							isDragEnabled
							isShiftStepEnabled
							shiftStep={1}
							min={0.3}
							max={10}
							step={0.1}
							value={triggerscrub}
						/>
					</>
					<div className="gspb_row" style={{ marginTop: 5 }}>
						<div className="gspb_row__col--10" style={{ padding: 0 }}>
							<span className="gspb_inspector_property-title">
								{__("Pin while scroll. It works only on frontend")}
							</span>
						</div>
						<div
							className="gspb_row__col--2"
							style={{ marginTop: "5px", padding: 0 }}
						>
							<ToggleControl
								checked={pinned}
								onChange={(value) => setAttributes({ pinned: value })}
							/>
						</div>
					</div>
					<div className="gspb_row">
						<div className="gspb_row__col--10" style={{ padding: 0 }}>
							<span className="gspb_inspector_property-title">
								{__("Enable overflow on pinned item")}
							</span>
						</div>
						<div
							className="gspb_row__col--2"
							style={{ marginTop: "5px", padding: 0 }}
						>
							<ToggleControl
								checked={pinspace}
								onChange={(value) => setAttributes({ pinspace: value })}
							/>
						</div>
					</div>
				</>
			)}
			{triggertype == 'hover' &&
				<div className="gspb_row">
					<div className="gspb_row__col--10" style={{ padding: 0 }}>
						<span className="gspb_inspector_property-title">
							{__("Pause on hover")}
						</span>
					</div>
					<div
						className="gspb_row__col--2"
						style={{ marginTop: "5px", padding: 0 }}
					>
						<ToggleControl
							checked={hoverpause}
							onChange={(value) => setAttributes({ hoverpause: value })}
						/>
					</div>
				</div>
			}
		</div>
	);
};

export default TriggerForm;


const TriggerDocLink = () => {
	return (
		<>
			Check documentation for available values - <a href="https://greensock.com/docs/v3/Plugins/ScrollTrigger" target="__blank"> ScrollTrigger Docs </a>
		</>
	)
}