// Import wp dependencies
const { __ } = wp.i18n;
import TriggerForm from './forms/TriggerForm';
import AnimationForm from './forms/AnimationForm';
import CommonForm from './forms/CommonForm';

const { InspectorControls } = wp.blockEditor;
const { PanelBody } = wp.components;
const {
	Spacing,
	Responsive,
	CssTransform,
	Position,
} = gspblib.collections;
const { InspectorTabs, InspectorTab } = gspblib.components;

const Inspector = (props) => {

	return (
		<InspectorControls>
			<InspectorTabs tabs={['general', 'advance']}>
				<InspectorTab key={'general'}>
					<PanelBody title={__("Common options")} initialOpen={true}>
						<CommonForm {...props} />
					</PanelBody>
					<PanelBody title={__("Animation options")} initialOpen={true}>
						<AnimationForm {...props} />
					</PanelBody>
					<PanelBody title={__("Trigger option")} initialOpen={false}>
						<TriggerForm {...props} />
					</PanelBody>
				</InspectorTab>
				<InspectorTab key={'advance'}>

					{ /* Css Transform */}
					<PanelBody title={__("Css Transform")} initialOpen={false}>
						<CssTransform attributeName="csstransform" {...props} />
					</PanelBody>

					{ /* Position Tab */}
					<PanelBody title={__("Position")} initialOpen={false}>
						<Position attributeName="position" {...props} />
					</PanelBody>
					{ /* Responsive */}
					<PanelBody title={__("Responsive")} initialOpen={false}>
						<Responsive attributeName="responsive" {...props} />
					</PanelBody>
					{ /* Spacing */}
					<PanelBody title={__("Spacing")} initialOpen={false}>
						<Spacing attributeName="spacing" autoMargin={true} {...props}/>
					</PanelBody>
				</InspectorTab>
			</InspectorTabs>
		</InspectorControls>
	);
}
export default Inspector;