/**
 * Set the block attributes
 * @type {Object}
 */
 const { collectionsObjects } = gspblib.helpers;

export default {

	id: {
		type: 'string',
		default: null,
	},
	inlineCssStyles: {
		type: 'string',
	},
    ease: {
        type: 'string',
    },
    delay: {
        type: 'number',
        default: null
    },
    duration: {
        type: 'number',
        default: 1
    },
    triggertype : {
        type: 'string',
        default: 'scroll'
    },

	// Css ID of custom trigger or custom class for Batch scroll.
    customtrigger: {
        type: 'string',
        default: null
    },

    // Css ID or class where to add animation.
    customobject: {
        type: 'string',
        default: null
    },

    triggerstart: {
        type: 'string',
        default: null
    },

    triggerend: {
        type: 'string',
        default: null
    },
    flipscale: {
        type: 'boolean',
    },    
    flipabsolute: {
        type: 'boolean',
    }, 
    flipstyle: {
        type: 'string',
        default: '{GREENSHIFT} .flipactive{position:fixed; bottom:0}{GREENSHIFT} .gs-flip-wrap{will-change:transform}'
    },   
    shadow: {
        type: 'object',
        default: collectionsObjects.shadow
    },
    responsive: {
		type: 'object',
		default: collectionsObjects.responsive
	},
    spacing: {
		type: 'object',
		default: collectionsObjects.spacing,
	},
    position: {
		type: 'object',
		default: collectionsObjects.position,
	},
	csstransform: {
		type: 'object',
		default: collectionsObjects.csstransform,
	},
}