// Import wp dependencies
const { __ } = wp.i18n;
import {
	PanelBody,
	ToggleControl,
	TextControl, Button, RangeControl
} from '@wordpress/components';
const { InspectorControls, BlockControls, AlignmentToolbar } = wp.blockEditor;
const {
	Spacing,
	Responsive,
	Background,
	Shadow,
	Border,
	Typography,
} = gspblib.collections;
const { InspectorTabs, InspectorTab, BlpgeColorPicker } = gspblib.components;

import { cloneDeep } from 'lodash';

const Inspector = (props) => {
	const {
		attributes: {
			allLabel,
			showAll,
			filterarray,
			id,
			flexAlign,
			gap,
			spaceFilter,
			checkboxColor,
			disableCheckbox,
			checkedState,
			reinit,
			minWidth
		},
		setAttributes
	} = props;

	const ALIGNMENT_CONTROLS = [
		{
			icon: 'editor-alignleft',
			title: __('Align Left', 'greenshift-gsap'),
			align: 'flex-start',
		},
		{
			icon: 'editor-aligncenter',
			title: __('Align Center', 'greenshift-gsap'),
			align: 'center',
		},
		{
			icon: 'editor-alignright',
			title: __('Align Right', 'greenshift-gsap'),
			align: 'flex-end',
		},
	];

	return (
		<>
			<InspectorControls>
				<InspectorTabs tabs={['general', 'advance']}>
					<InspectorTab key={'general'}>
						<PanelBody title={__("General", "greenshiftgsap")} initialOpen={true}>
							<div style={{ marginBottom: 10 }}>
								<Button
									isSecondary
									className="gspb-library-custom-preset-importbtn"
									isSmall
									onClick={
										() => {
											setAttributes({ reinit: !reinit });
										}
									}
								>
									{__('Re-initialize filters', 'greenshiftgsap')}
								</Button>
							</div>
							<ToggleControl
								checked={showAll}
								onChange={(value) => setAttributes({ showAll: value })}
								label={__("Enable checkbox for All filters", "greenshiftgsap")}
							/>
							{showAll &&
								<TextControl
									label={__("Label for All filter checkbox", "greenshiftgsap")}
									onChange={(value) => setAttributes({ allLabel: value })}
									value={allLabel}
								/>}
							<RangeControl
								label={__("Gap between checkboxes", 'greenshiftgsap')}
								value={gap}
								onChange={(value) => {
									setAttributes({
										gap: value,
									});
								}}
								trackColor='#2184f9'
								min={5}
								max={50}
								step={1}
							/>
							<RangeControl
								label={__("Min width for items", 'greenshiftgsap')}
								value={minWidth}
								onChange={(value) => {
									setAttributes({
										minWidth: value,
									});
								}}
								trackColor='#2184f9'
								min={5}
								max={500}
								step={1}
							/>
							<RangeControl
								label={__("Space after filters", 'greenshiftgsap')}
								value={spaceFilter}
								onChange={(value) => {
									setAttributes({
										spaceFilter: value,
									});
								}}
								trackColor='#2184f9'
								min={5}
								max={200}
								step={1}
							/>
							<ToggleControl
								checked={disableCheckbox}
								onChange={(value) => setAttributes({ disableCheckbox: value })}
								label={__("Disable checkbox visibility", "greenshiftgsap")}
							/>
							{!disableCheckbox &&
								<div className="gspb_row gspb_row--no-padding-col">
									<div className="gspb_row__col--6">
										<div>{__("Checkbox color")}</div>
									</div>
									<div
										className="gspb_row__col--6"
										style={{ textAlign: 'right', justifyContent: 'flex-end' }}
									>
										<BlpgeColorPicker
											color={checkboxColor}
											onChange={(value) =>
												setAttributes({ checkboxColor: value.rgb ? `rgba(${value.rgb.r}, ${value.rgb.g}, ${value.rgb.b}, ${value.rgb.a})` : value })
											}
										/>
									</div>
								</div>
							}
						</PanelBody>
						<PanelBody title={__("Filters", "greenshiftgsap")} initialOpen={true}>
							{filterarray.map((filter, index) => {
								const { label, id } = filter;

								return (
									<div className="gspb_inspector gs-inspector-form-inspector" style={{ border: "1px solid #eee", marginBottom: 10 }}>
										<div style={{ fontWeight: 'bold', padding: "5px 10px", textAlign: 'center', backgroundColor: "#edeff4" }}>{__("Filter", 'greenshift')} - {index + 1}</div>
										<div style={{ padding: 10 }}>
											<TextControl
												label={__("Label of filter", "greenshiftgsap")}
												value={label}
												onChange={(value) => {
													const filterarrayClone = cloneDeep(filterarray);
													filterarrayClone[index].label = value;
													setAttributes({
														filterarray: filterarrayClone
													});
												}}
											/>
											<TextControl
												label={__("Id of filter (required)", "greenshiftgsap")}
												value={id}
												help={__("Only latin symbols without spaces. This id must be unique and presented only once on page", "greenshiftgsap")}
												onChange={(value) => {
													const filterarrayClone = cloneDeep(filterarray);
													filterarrayClone[index].id = value;
													setAttributes({
														filterarray: filterarrayClone
													});
												}}
											/>
											<div style={{ marginBottom: 10 }}>{__("Copy next class to your items to assign them to this filter", "greenshiftgsap")}</div>
											<div style={{ padding: 5, backgroundColor: "#f3f3f3" }}>gs-flipfilter {id}</div>
										</div>
									</div>
								);
							})}

							<hr style={{ marginTop: 10, marginBottom: 20 }} />
							<div style={{ textAlign: 'center' }}>
								<Button
									isSecondary
									className="gspb-library-custom-preset-importbtn"
									isSmall
									onClick={
										() => {
											const filterarrayClone = cloneDeep(filterarray);
											let currentid = checkedState.length + 1;
											let blockid = id.slice(-5);
											filterarrayClone.push({
												label: "New filter",
												id: "filter" + blockid + currentid
											});
											const filterstateClone = cloneDeep(checkedState);
											filterstateClone.push(true);
											setAttributes({ filterarray: filterarrayClone, checkedState: filterstateClone, reinit: !reinit });
										}
									}
								>
									{__('Add filter', 'greenshiftgsap')}
								</Button>
								<Button
									isSmall
									isDestructive
									isSecondary
									onClick={
										() => {
											const filterarrayClone = cloneDeep(filterarray);
											filterarrayClone.pop();
											const filterstateClone = cloneDeep(checkedState);
											filterstateClone.pop();
											setAttributes({ filterarray: filterarrayClone, checkedState: filterstateClone, reinit: !reinit });
										}
									}
								>
									{__('Remove filter', 'greenshiftgsap')}
								</Button>

							</div>
						</PanelBody>
						{ /* Background Settings */}
						<PanelBody
							title={__("Background", 'greenshift')}
							initialOpen={false}
							className="gspb_inspector gspb_smallpadding_btn"
						>
							<Background
								attributeName="backgroundItem"
								exclude={['video', 'image', 'opacity']}
								{...props}
							/>
						</PanelBody>
						<PanelBody
							title={__("Background for active", 'greenshift')}
							initialOpen={false}
							className="gspb_inspector gspb_smallpadding_btn"
						>
							<Background
								attributeName="backgroundChecked"
								exclude={['video', 'image', 'opacity']}
								{...props}
							/>
						</PanelBody>

						{ /* Border Settings */}
						<PanelBody title={__("Border", 'greenshift')} initialOpen={false}>
							<Border attributeName="borderItem" {...props} />
						</PanelBody>
						<PanelBody title={__("Border for active", 'greenshift')} initialOpen={false}>
							<Border attributeName="borderChecked" {...props} />
						</PanelBody>

						{ /* Typography Settings */}
						<PanelBody title={__("Typography", 'greenshift')} initialOpen={false}>
							<Typography
								attributeName="typographyItem"
								includeHoverlinks={false}
								{...props}
							/>
						</PanelBody>
						<PanelBody title={__("Typography for active", 'greenshift')} initialOpen={false}>
							<Typography
								attributeName="typographyItem"
								includeHoverlinks={false}
								{...props}
							/>
						</PanelBody>

						{ /* Shadow Settings */}
						<PanelBody title={__("Shadow", 'greenshift')} initialOpen={false}>
							<Shadow attributeName="shadowItem" {...props} predefined={true} />
						</PanelBody>
						<PanelBody title={__("Shadow for active", 'greenshift')} initialOpen={false}>
							<Shadow attributeName="shadowChecked" {...props} predefined={true} />
						</PanelBody>

						{ /* Spacing */}
						<PanelBody title={__("Filter Spacing", "greenshiftgsap")} initialOpen={false}>
							<Spacing attributeName="spacingItem" autoMargin={false} {...props} />
						</PanelBody>
						<PanelBody title={__("Block Spacing", "greenshiftgsap")} initialOpen={false}>
							<Spacing attributeName="spacing" autoMargin={false} {...props} />
						</PanelBody>

					</InspectorTab>
					<InspectorTab key={'advance'}>
						{ /* Responsive */}
						<PanelBody title={__("Responsive and Custom CSS", "greenshiftgsap")} initialOpen={true}>
							<Responsive attributeName="responsive" {...props} />
						</PanelBody>
					</InspectorTab>
				</InspectorTabs>
			</InspectorControls>
			<BlockControls>
				<AlignmentToolbar
					value={flexAlign}
					onChange={(value) => {
						setAttributes({ flexAlign: value })
					}}
					alignmentControls={ALIGNMENT_CONTROLS}
				/>
			</BlockControls>
		</>
	);
}
export default Inspector;