import animationsTypes from '../../../animation-container/components/forms/Animationstype';
import { __ } from '@wordpress/i18n';
import { SelectControl, ToggleControl, BaseControl, __experimentalNumberControl as NumberControl } from '@wordpress/components';
const { GsbpFormLabel } = gspblib.components;

const AnimationForm = (props) => {
	const {
		attributes: {
			ease,
			delay,
			duration,
		},
		setAttributes,
	} = props;
	return (
		<>
			<div className="gs-inspector-form-inspector">
				<BaseControl className="gs-label-row">
					<NumberControl
						onChange={(value) => setAttributes({ duration: parseFloat(value) })}
						label={__('Duration')}
						isDragEnabled
						isShiftStepEnabled
						shiftStep={1}
						min={0.1}
						max={60}
						step={0.1}
						value={duration}
					/>
				</BaseControl>

				<BaseControl className="gs-label-row">
					<NumberControl
						onChange={(value) => setAttributes({ delay: parseFloat(value) })}
						label={__('Delay')}
						isDragEnabled
						isShiftStepEnabled
						shiftStep={1}
						min={0.1}
						max={20}
						step={0.1}
						value={delay}
					/>
				</BaseControl>
				<div className="gspb_row">

					<div className="gspb_row__col--7" style={{ padding: 0 }}>
						<GsbpFormLabel
							title={__("Ease type")}
							clearFun={() => setAttributes({ ease: null })}
						/>
					</div>
					<div className="gspb_row__col--5" style={{ padding: 0 }}>
						<SelectControl
							value={ease}
							options={animationsTypes}
							onChange={(value) => setAttributes({ ease: value })}
						/>
					</div>
				</div>
			</div>
		</>
	);
};
export default AnimationForm;
