const {
    gspb_spacing_cssGen,
    gspb_responsive_cssGen,
    gspb_position_cssGen,
    gspb_csstransform_cssGen,
    gspb_Css_Final,
    gspb_background_cssGen,
    gspb_shadow_cssGen,
    gspb_border_cssGen,
} = gspblib.utilities;
const { gspb_cssGen } = gspblib.helpers;

export const Sequencer = (props) => {

    const {
        attributes: {
            id,
            responsive,
            position,
            csstransform,
            spacing,
            delay,
            duration,
            triggertype,
            customtrigger,
            customobject,
            triggerstart,
            triggerend,
            triggeraction,
            images,
            ease,
            height,
            width,
            widthUnit,
            heightUnit,
            flexAlign,
            triggerscrub,
            pinned,
            pinspace,
            imageFit,
            background,
            border,
            shadow,
            repeatdelay,
            yoyo,
            loop,
            hoverpause,
            widthImage,
            heightImage,
            widthUnitImage,
            heightUnitImage
        },
        className,
        clientId
    } = props;


    let sequencerId = `gspb_gsap-${id}`;
    let sequencerClassName = `gs-sequencer-wrap ${className}`;
    let css_selector_by_user = '#' + sequencerId;
    let final_css;

    final_css = gspb_cssGen(
        css_selector_by_user + ' img',
        ['max-width', 'width', 'height', 'object-fit'],
        [
            ['100%'],
            [
                [widthImage[0] ? widthImage[0] + widthUnitImage[0] : null],
                [widthImage[1] ? widthImage[1] + widthUnitImage[1] : null],
                [widthImage[2] ? widthImage[2] + widthUnitImage[2] : null],
                [widthImage[3] ? widthImage[3] + widthUnitImage[3] : null],
            ],
            [
                [heightImage[0] ? heightImage[0] + heightUnitImage[0] : null],
                [heightImage[1] ? heightImage[1] + heightUnitImage[1] : null],
                [heightImage[2] ? heightImage[2] + heightUnitImage[2] : null],
                [heightImage[3] ? heightImage[3] + heightUnitImage[3] : null],
            ],
            [imageFit]
        ],
        final_css,
        false
    );
    final_css = gspb_cssGen(
        css_selector_by_user,
        ['width'],
        [
            [
                [width[0] ? width[0] + widthUnit[0] : null],
                [width[1] ? width[1] + widthUnit[1] : null],
                [width[2] ? width[2] + widthUnit[2] : null],
                [width[3] ? width[3] + widthUnit[3] : null],
            ],
        ],
        final_css,
        false
    );
    final_css = gspb_cssGen(
        css_selector_by_user,
        ['height'],
        [
            [
                [height[0] ? height[0] + heightUnit[0] : null],
                [height[1] ? height[1] + heightUnit[1] : null],
                [height[2] ? height[2] + heightUnit[2] : null],
                [height[3] ? height[3] + heightUnit[3] : null],
            ],
        ],
        final_css,
        false
    );

    // Responsive classes
    final_css = gspb_responsive_cssGen(
        responsive,
        css_selector_by_user,
        final_css,
    );

    // Position
    final_css = gspb_position_cssGen(
        position,
        'body.gspb-bodyfront ' + css_selector_by_user,
        final_css,
    );

    // Border
    final_css = gspb_border_cssGen(
        border,
        css_selector_by_user,
        final_css
    );

    // Background
    final_css = gspb_background_cssGen(
        background,
        css_selector_by_user,
        final_css
    );

    // Shadow
    final_css = gspb_shadow_cssGen(
        shadow,
        css_selector_by_user,
        final_css
    );

    // Css transform
    final_css = gspb_csstransform_cssGen(
        csstransform,
        css_selector_by_user,
        final_css,
        css_selector_by_user
    );

    // Spacing
    final_css = gspb_spacing_cssGen(
        spacing,
        css_selector_by_user,
        final_css
    );

    // Align
    final_css = gspb_cssGen(
        css_selector_by_user,
        ['justify-content'],
        [flexAlign],
        final_css
    );

    // Get The Stored CSS
    gspb_Css_Final(id, final_css, props);
    let editor_css = final_css;
    // Position
    editor_css = gspb_position_cssGen(
        position,
        '#block-' + clientId,
        editor_css,
    );
    let imagejsonarray = [];
    let imagejson = images.map((item, index) => {
        let currentimage = item.url;
        imagejsonarray.push(currentimage);

    });


    return (
        <div
            id={sequencerId}
            className={sequencerClassName}
            data-delay={(delay && Number.isFinite(delay)) ? delay : null}
            data-duration={(duration && Number.isFinite(duration)) ? duration : null}
            data-triggertype={triggertype}
            data-customtrigger={customtrigger ? customtrigger : null}
            data-customobject={customobject ? customobject : null}
            data-triggerstart={triggerstart ? triggerstart : null}
            data-triggerend={triggerend ? triggerend : null}
            data-triggeraction={triggeraction ? triggeraction : null}
            data-ease={ease ? ease : null}
            data-triggerscrub={triggerscrub ? triggerscrub : null}
            data-pinned={pinned ? 'yes' : null}
            data-pinspace={pinspace ? 'yes' : null}
            data-repeatdelay={repeatdelay ? 'yes' : null}
            data-yoyo={yoyo ? 'yes' : null}
            data-loop={loop ? 'yes' : null}
            data-hoverpause={hoverpause ? 'yes' : null}
            data-images={
                images.length ? JSON.stringify(imagejsonarray) : null
            }
            data-index='0'
            style={{ display: 'flex' }}
        >

            {images.length > 0 &&
                <img
                    key={images[0].id}
                    src={images[0].url}
                    alt={images[0].alt}
                    width={images[0].id ? images[0].sizes.full.width : null}
                    height={images[0].id ? images[0].sizes.full.height : null}
                />
            }

            {props.editor ? (
                <style
                    dangerouslySetInnerHTML={{
                        __html: editor_css,
                    }}
                />
            ) : null}
        </div>
    );
};

export default Sequencer;
