/**
 * Animation Container Block
 */

// Import Styles
import './editor.scss';

// Import wp dependencies
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
import { useEffect, useRef, useCallback } from '@wordpress/element';
import { seqicon } from './components/icons.js'

import attributes from './components/attributes';
import Sequencer from './components/Sequencer';
import Inspector from './components/inspector';


// Import gspb depenedencies
const { gspb_setBlockId } = gspblib.utilities;
const { BlockToolBar } = gspblib.components;

// Register Block
registerBlockType('greenshift-blocks/sequencer', {
    title: __('Sequence Animation'),
    description: __('Adds image sequence animations'),
    category: 'Greenshiftpro',
    keywords: [__('sequence'), __('animation'), __('effects')],
    icon: seqicon,
    supports: {
        reusable: true,
        anchor: true,
        align: ['wide', 'full'],
    },

    // Define attributes
    attributes: attributes,

    edit(props) {

        // Set Unique Block ID
        gspb_setBlockId(props);

        const {
            attributes: {
                ease,
                delay,
                duration,
                triggertype,
                customtrigger,
                customobject,
                triggerstart,
                triggerend,
                triggeraction,
                images,
                pinned,
                pinspace,
                yoyo,
                loop,
                repeatdelay,
                hoverpause
            },
            clientId
        } = props;

        const AnimationRef = useRef();
        const isInitialMount = useRef(true);
        const isStillMounted = useRef();
        const debounce = useCallback(
            _.debounce((_atts) => {
                isStillMounted.current = true;
                if (isStillMounted.current && AnimationRef.current) {
                    const gsapseq = AnimationRef.current.querySelector('.gs-sequencer-wrap');
                    if (gsapseq) {
                        let ownerDocument = AnimationRef.current.ownerDocument;
                        gsap.killTweensOf(gsapseq);
                        if(!ownerDocument.body.classList.contains('editor-styles-wrapper')){
                            GSSeqinit(gsapseq, false, true, ownerDocument);
                        }else{
                            GSSeqinit(gsapseq, true, false, ownerDocument);
                        }
                    }
                }
                return () => { isStillMounted.current = false; };
            }, 600),
            []
        );
        useEffect(() => {
            if (isInitialMount.current) {
                isInitialMount.current = false;
                if (AnimationRef.current) {
                    const gsapseq = AnimationRef.current.querySelector('.gs-sequencer-wrap');
                    if (gsapseq) {
                        let ownerDocument = AnimationRef.current.ownerDocument;
                        if(!ownerDocument.body.classList.contains('editor-styles-wrapper')){
                            GSSeqinit(gsapseq, false, true, ownerDocument);
                        }else{
                            GSSeqinit(gsapseq, true, false, ownerDocument);
                        }
                    }
                }
            } else {
                debounce(attributes);
            }
        }, [                
            ease,
            delay,
            duration,
            triggertype,
            customtrigger,
            customobject,
            triggerstart,
            triggerend,
            triggeraction,
            images,
            pinned,
            pinspace,
            yoyo,
            loop,
            repeatdelay,
            hoverpause]);

        return (
            <>
                <Inspector animationref={AnimationRef.current} {...props} />
                <BlockToolBar {...props}/>
                <div className="gs-animation" ref={AnimationRef}>
                    <Sequencer editor={true} {...props} />
                </div>
            </>
        );
    },

    save(props) {
        return (
            <Sequencer editor={false} {...props} />
        );
    },
});
