import { useEffect, useRef, useState } from '@wordpress/element';
import {
    InspectorControls, MediaUpload
} from '@wordpress/block-editor';
import { useSelect } from '@wordpress/data';
import {
    PanelBody,
    RangeControl,
    BaseControl,
    __experimentalNumberControl as NumberControl,
} from '@wordpress/components';
const { __ } = wp.i18n;

// Import blpge dependencies
const { BlockToolBar } = gspblib.components;
import Blob from './components/Blob';
import attributes from './components/attributes';

const {
    gspb_setBlockId
} = gspblib.utilities;
const { AnimationWrapper } = gspblib.collections;
const {
    Spacing,
    Responsive,
    CssTransform,
    Position,
    Animation,
} = gspblib.collections;
const { gspb_getDeviceStateIndex } = gspblib.utilities;
const { InspectorTabs, InspectorTab, Devices, BlpgeColorPicker, RadioAdvanced, CssUnits } = gspblib.components;
const { ExtraToolBar } = gspblib.editor;

function Edit(props) {
    const {
        attributes: {
            align,
            width,
            height,
            imageid,
            tensionPoints,
            minRadius,
            maxRadius,
            minDuration,
            maxDuration,
            numPoints,
            originalWidth,
            originalHeight,
            widthUnit,
            heightUnit,
            filltype,
            fillone,
            filltwo,
            offsetone,
            offsettwo,
            gradientRotate,
            stroke,
            strokewidth,
            imageurl,
            image_y,
            image_x,
            imagewidthUnit,
            imagewidth,
        },
        setAttributes
    } = props;

    gspb_setBlockId(props);
    const deviceStateIndex = gspb_getDeviceStateIndex();
    const [devstate, setdevState] = useState(0);

    const BlobRef = useRef();

    const imageObject = useSelect(
        (select) => {
            const { getMedia } = select('core');
            return imageid ? getMedia(imageid) : null;
        },
        [imageid]
    );

    if (imageObject) {
        let w = ''; let h = '';
        w = imageObject.media_details.width;
        h = imageObject.media_details.height;

        if (originalHeight != h || originalWidth != w) {
            setAttributes({ originalHeight: h, originalWidth: w });
        }
    }

    useEffect(() => {
        Blobinit(BlobRef.current);
    }, [tensionPoints,
        minRadius,
        maxRadius,
        minDuration,
        maxDuration,
        numPoints]
    );

    function Blobinit(ref) {
        let blobe = ref.querySelector('.gspb_blob');
        var id_scope = blobe.getAttribute('data-id');

        //console.log(elementSettings);

        var numPoints = parseInt(blobe.dataset.numpoints);
        var minRadius = parseInt(blobe.dataset.minradius);
        var maxRadius = parseInt(blobe.dataset.maxradius);
        var minDuration = parseInt(blobe.dataset.minduration);
        var maxDuration = parseInt(blobe.dataset.maxduration);
        var tensionPoints = parseInt(blobe.dataset.tensionpoints);

        var blob1 = createBlob({
            element: ref.querySelector("#blobpath-" + id_scope),
            numPoints: numPoints, //5,
            centerX: 300,
            centerY: 300,
            minRadius: minRadius, //200,
            maxRadius: maxRadius, //225,
            minDuration: minDuration,
            maxDuration: maxDuration,
            tensionPoints: tensionPoints,
        });
    }

    function createBlob(options) {
        var points = [];
        var path = options.element;
        var slice = (Math.PI * 2) / options.numPoints;
        var startAngle = random(Math.PI * 2);

        var tl = gsap.timeline({
            onUpdate: update
        });

        for (var i = 0; i < options.numPoints; i++) {
            var angle = startAngle + i * slice;
            var duration = random(options.minDuration, options.maxDuration);

            var point = {
                x: options.centerX + Math.cos(angle) * options.minRadius,
                y: options.centerY + Math.sin(angle) * options.minRadius
            };

            var tween = gsap.to(point, {
                duration: duration,
                x: options.centerX + Math.cos(angle) * options.maxRadius,
                y: options.centerY + Math.sin(angle) * options.maxRadius,
                repeat: -1,
                yoyo: true,
                ease: Sine.easeInOut
            });

            tl.add(tween, -random(duration));
            points.push(point);
        }

        options.tl = tl;
        options.points = points;

        function update() {
            path.setAttribute("d", cardinal(points, true, options.tensionPoints));
        }
        return options;
    }

    // Cardinal spline - a uniform Catmull-Rom spline with a tension option
    function cardinal(data, closed, tension) {

        if (data.length < 1) return "M0 0";
        if (tension == null) tension = 1;

        var size = data.length - (closed ? 0 : 1);
        var path = "M" + data[0].x + " " + data[0].y + " C";

        for (var i = 0; i < size; i++) {

            var p0, p1, p2, p3;

            if (closed) {
                p0 = data[(i - 1 + size) % size];
                p1 = data[i];
                p2 = data[(i + 1) % size];
                p3 = data[(i + 2) % size];

            } else {
                p0 = i == 0 ? data[0] : data[i - 1];
                p1 = data[i];
                p2 = data[i + 1];
                p3 = i == size - 1 ? p2 : data[i + 2];
            }

            var x1 = p1.x + (p2.x - p0.x) / 6 * tension;
            var y1 = p1.y + (p2.y - p0.y) / 6 * tension;

            var x2 = p2.x - (p3.x - p1.x) / 6 * tension;
            var y2 = p2.y - (p3.y - p1.y) / 6 * tension;

            path += " " + x1 + " " + y1 + " " + x2 + " " + y2 + " " + p2.x + " " + p2.y;
        }

        return closed ? path + "z" : path;
    }

    function random(min, max) {
        if (max == null) { max = min; min = 0; }
        if (min > max) { var tmp = min; min = max; max = tmp; }
        return min + (max - min) * Math.random();
    }

    return (
        <>
            <ExtraToolBar
                editOnly={true}
                inner={true}
                gray={true}
                {...props}
            />
            <InspectorControls>
                <div className="gspb_inspector">
                    <InspectorTabs tabs={['general', 'advance']}>
                        <InspectorTab key={'general'}>
                            <PanelBody title={__("Blob")} initialOpen={true}>

                                <RangeControl
                                    label={__("Tension points")}
                                    value={tensionPoints}
                                    onChange={(value) => { setAttributes({ tensionPoints: value }); }}
                                    trackColor='#2184f9'
                                    min={0}
                                    max={10}
                                    step={0.1}
                                />
                                <RangeControl
                                    label={__("Num Points")}
                                    value={numPoints}
                                    onChange={(value) => { setAttributes({ numPoints: value, }); }}
                                    trackColor='#2184f9'
                                    min={3}
                                    max={100}
                                    step={1}
                                />
                                <RangeControl
                                    label={__("Min Radius")}
                                    value={minRadius}
                                    onChange={(value) => { setAttributes({ minRadius: value, }); }}
                                    trackColor='#2184f9'
                                    min={10}
                                    max={600}
                                    step={1}
                                />
                                <RangeControl
                                    label={__("Max Radius")}
                                    help={__("Number from Min Radius")}
                                    value={maxRadius}
                                    onChange={(value) => { setAttributes({ maxRadius: value, }); }}
                                    trackColor='#2184f9'
                                    min={1}
                                    max={500}
                                    step={1}
                                />
                                <RangeControl
                                    label={__("Min Duration")}
                                    value={minDuration}
                                    onChange={(value) => { setAttributes({ minDuration: value, }); }}
                                    trackColor='#2184f9'
                                    min={0.1}
                                    max={20}
                                    step={0.1}
                                />
                                <RangeControl
                                    label={__("Max Duration")}
                                    help={__("Number from Min Duration")}
                                    value={maxDuration}
                                    onChange={(value) => { setAttributes({ maxDuration: value, }); }}
                                    trackColor='#2184f9'
                                    min={0.1}
                                    max={20}
                                    step={0.1}
                                />

                                <div className="gspb_inspector_options-set" style={{ marginBottom: 15 }}>
                                    <div className="gspb_row gspb_row--no-padding-col">
                                        <div className="gspb_row__col--7">
                                            <span className="gspb_inspector_property-title">
                                                {__('Width')}
                                                <Devices
                                                    className="gspb_inspector_device-icons--small"
                                                    onChange={() =>
                                                        setdevState(!devstate)
                                                    }
                                                />
                                            </span>
                                        </div>
                                        <div
                                            className="gspb_row__col--5"
                                            style={{
                                                justifyContent: 'space-between',
                                            }}
                                        >
                                            <div>

                                                <CssUnits
                                                    units={['px', '%']}
                                                    attribute={
                                                        widthUnit[deviceStateIndex] ==
                                                            null
                                                            ? ''
                                                            : widthUnit[deviceStateIndex]
                                                    }
                                                    onChange={(value) => {
                                                        let currentValue = widthUnit.slice();
                                                        currentValue[
                                                            deviceStateIndex
                                                        ] = value;
                                                        setAttributes({
                                                            widthUnit: currentValue,
                                                        });
                                                    }}
                                                />
                                                <div
                                                    className="gspb_inspector_clear_btn--right"
                                                    onClick={() => {
                                                        let currentValue = width.slice();
                                                        currentValue[
                                                            deviceStateIndex
                                                        ] = null;
                                                        setAttributes({
                                                            width: currentValue,
                                                        });
                                                    }}
                                                >
                                                    <i className="rhicon rhi-undo" />
                                                </div>
                                            </div>
                                        </div>
                                        <div style={{ width: '100%' }}>
                                            <RangeControl
                                                value={
                                                    width[deviceStateIndex] == null
                                                        ? ''
                                                        : width[deviceStateIndex]
                                                }
                                                onChange={(value) => {
                                                    let currentValue = width.slice();
                                                    currentValue[
                                                        deviceStateIndex
                                                    ] = value;
                                                    setAttributes({
                                                        width: currentValue,
                                                    });
                                                }}
                                                trackColor='#2184f9'
                                                min={
                                                    widthUnit[deviceStateIndex] ==
                                                        'px'
                                                        ? 10
                                                        : 1
                                                }
                                                max={
                                                    widthUnit[deviceStateIndex] ==
                                                        'px'
                                                        ? 1800
                                                        : 100
                                                }
                                            />
                                        </div>
                                    </div>
                                </div>
                                <div className="gspb_inspector_options-set" style={{ marginBottom: 15 }}>
                                    <div className="gspb_row gspb_row--no-padding-col">
                                        <div className="gspb_row__col--7">
                                            <span className="gspb_inspector_property-title">
                                                {__('Height')}
                                                <Devices
                                                    className="gspb_inspector_device-icons--small"
                                                    onChange={() =>
                                                        setdevState(!devstate)
                                                    }
                                                />
                                            </span>
                                        </div>
                                        <div
                                            className="gspb_row__col--5"
                                            style={{
                                                justifyContent: 'space-between',
                                            }}
                                        >
                                            <div>

                                                <CssUnits
                                                    units={['px', '%']}
                                                    attribute={
                                                        heightUnit[deviceStateIndex] ==
                                                            null
                                                            ? ''
                                                            : heightUnit[deviceStateIndex]
                                                    }
                                                    onChange={(value) => {
                                                        let currentValue = heightUnit.slice();
                                                        currentValue[
                                                            deviceStateIndex
                                                        ] = value;
                                                        setAttributes({
                                                            heightUnit: currentValue,
                                                        });
                                                    }}
                                                />
                                                <div
                                                    className="gspb_inspector_clear_btn--right"
                                                    onClick={() => {
                                                        let currentValue = height.slice();
                                                        currentValue[
                                                            deviceStateIndex
                                                        ] = null;
                                                        setAttributes({
                                                            height: currentValue,
                                                        });
                                                    }}
                                                >
                                                    <i className="rhicon rhi-undo" />
                                                </div>
                                            </div>
                                        </div>
                                        <div style={{ width: '100%' }}>
                                            <RangeControl
                                                value={
                                                    height[deviceStateIndex] == null
                                                        ? ''
                                                        : height[deviceStateIndex]
                                                }
                                                onChange={(value) => {
                                                    let currentValue = height.slice();
                                                    currentValue[
                                                        deviceStateIndex
                                                    ] = value;
                                                    setAttributes({
                                                        height: currentValue,
                                                    });
                                                }}
                                                trackColor='#2184f9'
                                                min={
                                                    heightUnit[deviceStateIndex] ==
                                                        'px'
                                                        ? 10
                                                        : 1
                                                }
                                                max={
                                                    heightUnit[deviceStateIndex] ==
                                                        'px'
                                                        ? 1800
                                                        : 100
                                                }
                                            />
                                        </div>
                                    </div>
                                </div>

                                <RadioAdvanced
                                    value={align[deviceStateIndex]}
                                    label={__('Align')}
                                    fluid={''}
                                    onChange={(value) => {
                                        let currentValue = align.slice();
                                        currentValue[
                                            deviceStateIndex
                                        ] = value;
                                        setAttributes({
                                            align: currentValue,
                                        });
                                    }}
                                    options={[
                                        { icon: 'dashicon dashicons dashicons-editor-alignleft', value: 'left', title: __('Left') },
                                        { icon: 'dashicon dashicons dashicons-editor-aligncenter', value: 'center', title: __('Center') },
                                        { icon: 'dashicon dashicons dashicons-editor-alignright', value: 'right', title: __('Right') },
                                    ]}
                                />
                                <RadioAdvanced
                                    label={__('Fill by')}
                                    fluid={''}
                                    value={filltype}
                                    onChange={(value) => { setAttributes({ filltype: value }) }}
                                    options={[
                                        { label: __('color'), value: 'color', title: __('Color') },
                                        { label: __('image'), value: 'image', title: __('Image') },
                                    ]}
                                />
                                {filltype == 'color' && (
                                    <>
                                        <div style={{ marginTop: 15, marginBottom: 10 }}>
                                            {__("Fill Color")}
                                        </div>
                                        <div className="gspb_row gspb_row--no-padding-col" style={{ marginBottom: 6 }}>
                                            <div className="gspb_row__col--6">
                                                <BlpgeColorPicker
                                                    color={fillone}
                                                    onChange={(value) =>
                                                        setAttributes({ fillone: value.rgb ? `rgba(${value.rgb.r}, ${value.rgb.g}, ${value.rgb.b}, ${value.rgb.a})` : value })
                                                    }
                                                />
                                            </div>
                                            <div
                                                className="gspb_row__col--6"
                                                style={{ textAlign: 'right', justifyContent: 'flex-end' }}
                                            >
                                                <BlpgeColorPicker
                                                    color={filltwo}
                                                    onChange={(value) =>
                                                        setAttributes({ filltwo: value.rgb ? `rgba(${value.rgb.r}, ${value.rgb.g}, ${value.rgb.b}, ${value.rgb.a})` : value })
                                                    }
                                                />
                                            </div>
                                        </div>
                                        {filltwo && (
                                            <>
                                                <div className="gspb_row gspb_row--no-padding-col">
                                                    <div className="gspb_row__col--5">
                                                        <BaseControl help={__('Offset 1')}>
                                                            <NumberControl
                                                                value={offsetone}
                                                                isDragEnabled
                                                                isShiftStepEnabled
                                                                shiftStep={10}
                                                                onChange={(value) =>
                                                                    setAttributes({ offsetone: parseInt(value) })
                                                                }
                                                                min={0}
                                                                max={100}
                                                                default={0}
                                                            />
                                                        </BaseControl>
                                                    </div>
                                                    <div
                                                        className="gspb_row__col--5"
                                                        style={{ textAlign: 'right', justifyContent: 'flex-end' }}
                                                    >
                                                        <BaseControl help={__('Offset 2')}>
                                                            <NumberControl
                                                                value={offsettwo}
                                                                isDragEnabled
                                                                isShiftStepEnabled
                                                                shiftStep={10}
                                                                onChange={(value) => {
                                                                    setAttributes({ offsettwo: parseInt(value) })
                                                                }
                                                                }
                                                                min={0}
                                                                max={100}
                                                                default={100}
                                                            />
                                                        </BaseControl>
                                                    </div>
                                                </div>
                                                <BaseControl className="gs-label-row">
                                                    <NumberControl
                                                        label={__('Gradient rotate')}
                                                        value={gradientRotate}
                                                        isDragEnabled
                                                        isShiftStepEnabled
                                                        shiftStep={10}
                                                        onChange={(value) =>
                                                            setAttributes({ gradientRotate: value })
                                                        }
                                                        min={0}
                                                        max={360}
                                                    />
                                                </BaseControl>
                                            </>
                                        )}
                                    </>
                                )}
                                {filltype == 'image' && (
                                    <>
                                        <div className="gspb_inspector_options-set">
                                            <span className="gspb_inspector_property-title">
                                                {__('Background Image')}
                                            </span>
                                            <div
                                                className="gspb_inspector_clear_btn--right"
                                                onClick={() =>
                                                    setAttributes({ imageurl: null })
                                                }
                                            >
                                                <i className="rhicon rhi-undo" />
                                            </div>
                                            <div style={{ display: 'block' }}>
                                                <MediaUpload
                                                    onSelect={(imgObject) => {
                                                        setAttributes({ imageurl: imgObject.sizes.full.url, imageid: imgObject.id })
                                                    }
                                                    }
                                                    type="image"
                                                    render={({ open }) => (
                                                        <button
                                                            className="gspb_inspector_btn gspb_inspector_btn--medium"
                                                            onClick={open}
                                                        >
                                                            {__('Choose an image')}
                                                            <i className="rhicon rhi-image-regular" />
                                                        </button>
                                                    )}
                                                />
                                                <div
                                                    className="gspb_inspector_img_preview"
                                                    style={{ display: 'block' }}
                                                >
                                                    <img
                                                        src={
                                                            imageurl
                                                                ? imageurl
                                                                : null
                                                        }
                                                    />
                                                </div>
                                            </div>

                                        </div>
                                        <div style={{ marginTop: 15 }}></div>
                                        <RangeControl
                                            label={__("X offset")}
                                            value={image_x}
                                            onChange={(value) => { setAttributes({ image_x: value, }); }}
                                            trackColor='#2184f9'
                                            min={-1000}
                                            max={1000}
                                            step={1}
                                        />
                                        <RangeControl
                                            label={__("Y offset")}
                                            value={image_y}
                                            onChange={(value) => { setAttributes({ image_y: value, }); }}
                                            trackColor='#2184f9'
                                            min={-1000}
                                            max={1000}
                                            step={1}
                                        />
                                        <div className="gspb_inspector_options-set" style={{ marginBottom: 15 }}>
                                            <div className="gspb_row gspb_row--no-padding-col">
                                                <div className="gspb_row__col--7">
                                                    <span className="gspb_inspector_property-title">
                                                        {__('Image max Width')}
                                                    </span>
                                                </div>
                                                <div
                                                    className="gspb_row__col--5"
                                                    style={{
                                                        justifyContent: 'space-between',
                                                    }}
                                                >
                                                    <div>

                                                        <CssUnits
                                                            units={['px', '%']}
                                                            attribute={imagewidthUnit}
                                                            onChange={(value) => { setAttributes({ imagewidthUnit: value }) }}
                                                        />
                                                        <div
                                                            className="gspb_inspector_clear_btn--right"
                                                            onClick={() => {
                                                                setAttributes({
                                                                    imagewidth: null,
                                                                });
                                                            }}
                                                        >
                                                            <i className="rhicon rhi-undo" />
                                                        </div>
                                                    </div>
                                                </div>
                                                <div style={{ width: '100%' }}>
                                                    <RangeControl
                                                        value={imagewidth}
                                                        onChange={(value) => { setAttributes({ imagewidth: value }) }}
                                                        trackColor='#2184f9'
                                                        min={
                                                            imagewidthUnit ==
                                                                'px'
                                                                ? 10
                                                                : 1
                                                        }
                                                        max={
                                                            imagewidthUnit ==
                                                                'px'
                                                                ? 1800
                                                                : 100
                                                        }
                                                    />
                                                </div>
                                            </div>
                                        </div>
                                    </>
                                )}
                                <div className="gspb_row gspb_row--no-padding-col">
                                    <div className="gspb_row__col--6">
                                        <div>{__("Stroke color")}</div>
                                    </div>
                                    <div
                                        className="gspb_row__col--6"
                                        style={{ textAlign: 'right', justifyContent: 'flex-end' }}
                                    >
                                        <BlpgeColorPicker
                                            color={stroke}
                                            onChange={(value) =>
                                                setAttributes({ stroke: value.rgb ? `rgba(${value.rgb.r}, ${value.rgb.g}, ${value.rgb.b}, ${value.rgb.a})` : value })
                                            }
                                        />
                                    </div>
                                </div>
                                <div style={{ marginTop: 8 }}></div>
                                <BaseControl className="gs-label-row">
                                    <NumberControl
                                        label={__('Stroke Width')}
                                        value={strokewidth}
                                        isDragEnabled
                                        isShiftStepEnabled
                                        shiftStep={1}
                                        onChange={(value) =>
                                            setAttributes({ strokewidth: parseInt(value) })
                                        }
                                        min={0}
                                        max={30}
                                    />
                                </BaseControl>

                            </PanelBody>

                            { /* Spacing */}
                            <PanelBody title={__("Spacing")} initialOpen={false}>
                                <Spacing attributeName="spacing" overflow={false} {...props} />
                            </PanelBody>

                        </InspectorTab>
                        <InspectorTab key={'advance'}>
                            { /* Animations Tab */}
                            <PanelBody title={__("Animation")} initialOpen={true}>
                                <Animation
                                    attributeName="animation"
                                    {...props}
                                />
                            </PanelBody>

                            { /* Css Transform */}
                            <PanelBody title={__("Css Transform")} initialOpen={false}>
                                <CssTransform attributeName="csstransform" {...props} />
                            </PanelBody>

                            { /* Position Tab */}
                            <PanelBody title={__("Position")} initialOpen={false}>
                                <Position attributeName="position" {...props} />
                            </PanelBody>
                            { /* Responsive */}
                            <PanelBody title={__("Responsive")} initialOpen={false}>
                                <Responsive attributeName="responsive" {...props} />
                            </PanelBody>
                        </InspectorTab>
                    </InspectorTabs>
                </div>

            </InspectorControls>
            <BlockToolBar {...props} />
            <AnimationWrapper attributes={attributes} props={props}>
                <div ref={BlobRef}>
                    <Blob editor={true} {...props} />
                </div>
            </AnimationWrapper>
        </>
    );

}

export default Edit;