/**
 * Lottie
 */


// Import wp dependencies
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
import { useEffect, useRef, useState } from '@wordpress/element';

import {
	InspectorControls,
	BlockControls,
	AlignmentToolbar,
	MediaUpload, MediaUploadCheck, useBlockProps
} from '@wordpress/block-editor';

import {
	PanelBody,
	RangeControl,
	ToggleControl,
	TextControl,
	Button,
	SelectControl,
	BaseControl
} from '@wordpress/components';

// Import block dependencies
import blockIcon from './icon';

const {
	Spacing,
	Responsive,
	CssTransform,
	Position,
} = gspblib.collections;
const { BlockToolBar } = gspblib.components;
const { collectionsObjects } = gspblib.helpers;
const {
	gspb_spacing_cssGen,
	gspb_position_cssGen,
	gspb_csstransform_cssGen,
	gspb_responsive_cssGen,
	gspb_Css_Final
} = gspblib.utilities;

// Import gspb depenedencies
const { gspb_setBlockId, gspb_getDeviceStateIndex } = gspblib.utilities;
const { InspectorTabs, InspectorTab, Devices, CssUnits } = gspblib.components;
const { gspb_cssGen } = gspblib.helpers;

// Register Block
registerBlockType('greenshift-blocks/lottie', {
	title: __('Lottie smart loader'),
	description: __('Add lottie with smart loading'),
	icon: blockIcon,
	category: 'Greenshiftpro',
	keywords: [__('lottie'), __('animation')],
	supports: {
		align: ['wide', 'full']
	},

	// Define attributes
	attributes: {
		source: {
			type: 'string',
			default: 'https://assets4.lottiefiles.com/private_files/lf30_nlhswxdd.json'
		},
		loop: {
			type: 'boolean',
			default: true
		},
		loadnow: {
			type: 'boolean',
			default: false
		},
		disablelazy: {
			type: 'boolean',
		},
		bounce: {
			type: 'boolean',
			default: false
		},
		autoplay: {
			type: 'boolean',
			default: true
		},
		background: {
			type: 'string',
			default: '#ffffff00'
		},
		imageid: {
			type: 'number'
		},
		imageurl: {
			type: 'string'
		},
		imagealt: {
			type: 'string',
			default: ''
		},
		mode: {
			type: 'string',
			default: ''
		},
		modetypescroll: {
			type: 'string',
			default: 'play'
		},
		modetypecursor: {
			type: 'string',
			default: 'hover'
		},
		width: {
			type: 'array',
			default: [300, null, null, null],
		},
		widthUnit: {
			type: 'array',
			default: ['px', 'px', 'px', 'px'],
		},
		height: {
			type: 'array',
			default: [300, null, null, null],
		},
		heightUnit: {
			type: 'array',
			default: ['px', 'px', 'px', 'px'],
		},
		speed: {
			type: 'number',
			default: 1
		},
		visibilityStart: {
			type: 'number',
			default: 0.1
		},
		visibilityEnd: {
			type: 'number',
			default: 1
		},
		frameStart: {
			type: 'number',
			default: 0
		},
		frameEnd: {
			type: 'number',
			default: 100
		},
		align: {
			type: 'string',
			default: 'center'
		},
		id: {
			type: 'string',
			default: null,
		},
		inlineCssStyles: {
			type: 'string',
		},
		spacing: {
			type: 'object',
			default: collectionsObjects.spacing,
		},
		animation: {
			type: 'object',
			default: collectionsObjects.animation,
		},
		responsive: {
			type: 'object',
			default: collectionsObjects.responsive
		},
		position: {
			type: 'object',
			default: collectionsObjects.position,
		},
		csstransform: {
			type: 'object',
			default: collectionsObjects.csstransform,
		},
	},

	edit(props) {
		const {
			attributes: {
				source, loop, autoplay, background, mode, visibilityStart, visibilityEnd, frameStart, frameEnd, width, height, speed, imageurl, imageid, align, modetypescroll, modetypecursor, bounce, id, widthUnit, heightUnit,
				spacing,
				animation,
				responsive,
				position,
				csstransform,
				disablelazy,
				loadnow
			},
			setAttributes,
			clientId
		} = props;

		// Set Unique Block ID
		gspb_setBlockId(props);

		const deviceStateIndex = gspb_getDeviceStateIndex();
		const [devstate, setdevState] = useState(0);

		const LottieRef = useRef();
		const isInitialMount = useRef(true);
		const [loadedlottie, setLoaded] = useState(false);
		const lottieId = `gs-lottie-${id}`;
		let css_selector_by_user = `.${lottieId}`;

		useEffect(() => {
			const currentRef = LottieRef.current;
			if (isInitialMount.current) {
				isInitialMount.current = false;
				const { ownerDocument } = currentRef;
				const body = ownerDocument.body;

				const onInteraction = () => {
					if (loadedlottie === true) {
						return;
					}
					setLoaded(true);
					if (body.querySelector('#gs-lottie-loader') === null) {
						const LottieScript = document.createElement("script");
						LottieScript.src = "https://unpkg.com/@lottiefiles/lottie-player@latest/dist/lottie-player.js";
						LottieScript.id = 'gs-lottie-loader';
						body.appendChild(LottieScript);
					}
				};

				onInteraction();
			} else {
				let currentNode = currentRef;
				currentNode.classList.add('lottie-loaded');
				let currentViewer = currentNode.querySelector("lottie-player");
				if (currentViewer && typeof currentViewer.setLooping !== "undefined") {
					currentViewer.load(source);
					currentViewer.setSpeed(speed);
				}
			}

		}, [source, speed]);

		const ALIGNMENT_CONTROLS = [
			{
				icon: 'editor-alignleft',
				title: __('Align Left', 'greenshiftgsap'),
				align: 'flex-start',
			},
			{
				icon: 'editor-aligncenter',
				title: __('Align Center', 'greenshiftgsap'),
				align: 'center',
			},
			{
				icon: 'editor-alignright',
				title: __('Align Right', 'greenshiftgsap'),
				align: 'flex-end',
			},
		];

		const takeSnapshot = () => {
			let currentNode = LottieRef.current;
			if (currentNode) {
				let viewer = currentNode.querySelector("lottie-player");
				if (typeof viewer.snapshot != 'undefined') {
					viewer.seek(1);
					viewer.snapshot(true);
				}
			}
		}

		const blockProps = useBlockProps({
			className: `gs-lottieloader ${lottieId}`,
			ref: LottieRef,
			style: { display: "flex", justifyContent: align },
		});

		let Lottieprops = {
			mode: bounce ? "bounce" : "normal",
		}

		// Get The Stored CSS
		let final_css = `.gspb-bodyfront .${lottieId} img{transition: opacity 1s ease-in; position:absolute}.gspb-bodyfront .${lottieId}.lottie-loaded img{opacity:0}`;
		final_css = gspb_cssGen(
			`.${lottieId} img, .${lottieId} lottie-player`,
			['width'],
			[
				[
					[width[0] ? width[0] + widthUnit[0] + '!important' : null],
					[width[1] ? width[1] + widthUnit[1] + '!important' : null],
					[width[2] ? width[2] + widthUnit[2] + '!important' : null],
					[width[3] ? width[3] + widthUnit[3] + '!important' : null],
				],
			],
			final_css,
			false
		);

		final_css = gspb_cssGen(
			`.${lottieId} img, .${lottieId} lottie-player`,
			['height'],
			[
				[
					[height[0] ? height[0] + heightUnit[0] + '!important' : null],
					[height[1] ? height[1] + heightUnit[1] + '!important' : null],
					[height[2] ? height[2] + heightUnit[2] + '!important' : null],
					[height[3] ? height[3] + heightUnit[3] + '!important' : null],
				],
			],
			final_css,
			false
		);
		// Responsive classes
		final_css = gspb_responsive_cssGen(
			responsive,
			css_selector_by_user,
			final_css,
		);

		// Position
		final_css = gspb_position_cssGen(
			position,
			'body.gspb-bodyfront ' + css_selector_by_user,
			final_css,
		);

		// Css transform
		final_css = gspb_csstransform_cssGen(
			csstransform,
			css_selector_by_user,
			final_css,
			animation
		);

		// Spacing
		final_css = gspb_spacing_cssGen(
			spacing,
			css_selector_by_user,
			final_css
		);

		gspb_Css_Final(id, final_css, props);
		let editor_css = final_css;

		// Position
		editor_css = gspb_position_cssGen(
			position,
			'#block-' + clientId,
			editor_css,
		);

		return [

			<InspectorControls>
				<div className="gspb_inspector">
					<InspectorTabs tabs={['general', 'advance']}>
						<InspectorTab key={'general'}>
							<PanelBody
								initialOpen={true}
								title={__('Lottie loader')}
							>
								<TextControl
									label={__('File URL or lottie Json')}
									value={source}
									onChange={(value) => {
										setAttributes({
											source: value
										});
									}}
								/>
								<div style={{ marginTop: 10, marginBottom: 10 }}>{__("Fallback image")}</div>
								<MediaUploadCheck>
									<MediaUpload
										onSelect={(imgObject) => {
											setAttributes({
												imageid: imgObject.id,
												imageurl: imgObject.url,
												imagealt: imgObject.alt
											});
										}

										}
										type="image"
										render={({ open }) => (
											<Button isSmall variant="secondary" onClick={open}>
												{__('Choose an image')}
											</Button>
										)}
									/>
								</MediaUploadCheck>
								<div style={{ display: 'block' }}>
									<img
										style={{ maxWidth: "100%" }}
										src={
											imageurl
												? imageurl
												: null
										}
									/>
								</div>
								{imageurl &&
									<BaseControl help={__("Recommended if your block is in first screen", 'greenshiftgsap')}>

										<ToggleControl
											label={__('Disable image lazy load', 'greenshiftgsap')}
											checked={disablelazy}
											onChange={(disablelazy) => {
												setAttributes({ disablelazy })
											}}
										/>
									</BaseControl>
								}
								<div style={{ display: 'block', marginBottom: 15 }}>

									<Button isSmall variant="secondary" onClick={takeSnapshot}>
										{__('Take snapshot image of current lottie', 'greenshiftgsap')}
									</Button>
								</div>
								<BaseControl help={__("This will reduce web vitals score. We recommend to use fallback image instead", 'greenshiftgsap')}>

								<ToggleControl
											label={__('Enable immediate loading', 'greenshiftgsap')}
											checked={loadnow}
											onChange={(loadnow) => {
												setAttributes({ loadnow })
											}}
										/>
								</BaseControl>
								<ToggleControl
									label={__('Autoplay', 'greenshiftgsap')}
									checked={autoplay}
									onChange={(autoplay) => {
										setAttributes({ autoplay })
									}}
								/>
								<ToggleControl
									label={__('Loop', 'greenshiftgsap')}
									checked={loop}
									onChange={(loop) => {
										setAttributes({ loop });
									}}
								/>
								<ToggleControl
									label={__('Bounce play mode', 'greenshiftgsap')}
									checked={bounce}
									onChange={(bounce) => {
										setAttributes({ bounce });
									}}
								/>
								<div className="gspb_inspector_options-set" style={{ marginBottom: 15 }}>
									<div className="gspb_row gspb_row--no-padding-col">
										<div className="gspb_row__col--7">
											<span className="gspb_inspector_property-title">
												{__('Width')}
												<Devices
													className="gspb_inspector_device-icons--small"
													onChange={() =>
														setdevState(!devstate)
													}
												/>
											</span>
										</div>
										<div
											className="gspb_row__col--5"
											style={{
												justifyContent: 'space-between',
											}}
										>
											<div>

												<CssUnits
													units={['px', '%', 'vw']}
													attribute={
														widthUnit[deviceStateIndex] ==
															null
															? ''
															: widthUnit[deviceStateIndex]
													}
													onChange={(value) => {
														let currentValue = widthUnit.slice();
														currentValue[
															deviceStateIndex
														] = value;
														setAttributes({
															widthUnit: currentValue,
														});
													}}
												/>
												<div
													className="gspb_inspector_clear_btn--right"
													onClick={() => {
														let currentValue = width.slice();
														currentValue[
															deviceStateIndex
														] = null;
														setAttributes({
															width: currentValue,
														});
													}}
												>
													<i className="rhicon rhi-undo" />
												</div>
											</div>
										</div>
										<div style={{ width: '100%' }}>
											<RangeControl
												value={
													width[deviceStateIndex] == null
														? ''
														: width[deviceStateIndex]
												}
												onChange={(value) => {
													let currentValue = width.slice();
													currentValue[
														deviceStateIndex
													] = value;
													setAttributes({
														width: currentValue,
													});
												}}
												trackColor='#2184f9'
												min={
													widthUnit[deviceStateIndex] ==
														'px'
														? 10
														: 1
												}
												max={
													widthUnit[deviceStateIndex] ==
														'px'
														? 1800
														: 100
												}
											/>
										</div>
									</div>
								</div>
								<div className="gspb_inspector_options-set" style={{ marginBottom: 15 }}>
									<div className="gspb_row gspb_row--no-padding-col">
										<div className="gspb_row__col--7">
											<span className="gspb_inspector_property-title">
												{__('Height')}
												<Devices
													className="gspb_inspector_device-icons--small"
													onChange={() =>
														setdevState(!devstate)
													}
												/>
											</span>
										</div>
										<div
											className="gspb_row__col--5"
											style={{
												justifyContent: 'space-between',
											}}
										>
											<div>

												<CssUnits
													units={['px', '%', 'vh']}
													attribute={
														heightUnit[deviceStateIndex] ==
															null
															? ''
															: heightUnit[deviceStateIndex]
													}
													onChange={(value) => {
														let currentValue = heightUnit.slice();
														currentValue[
															deviceStateIndex
														] = value;
														setAttributes({
															heightUnit: currentValue,
														});
													}}
												/>
												<div
													className="gspb_inspector_clear_btn--right"
													onClick={() => {
														let currentValue = height.slice();
														currentValue[
															deviceStateIndex
														] = null;
														setAttributes({
															height: currentValue,
														});
													}}
												>
													<i className="rhicon rhi-undo" />
												</div>
											</div>
										</div>
										<div style={{ width: '100%' }}>
											<RangeControl
												value={
													height[deviceStateIndex] == null
														? ''
														: height[deviceStateIndex]
												}
												onChange={(value) => {
													let currentValue = height.slice();
													currentValue[
														deviceStateIndex
													] = value;
													setAttributes({
														height: currentValue,
													});
												}}
												trackColor='#2184f9'
												min={
													heightUnit[deviceStateIndex] ==
														'px'
														? 10
														: 1
												}
												max={
													heightUnit[deviceStateIndex] ==
														'px'
														? 1800
														: 100
												}
											/>
										</div>
									</div>
								</div>
								<RangeControl
									label={__("Speed", 'greenshiftgsap')}
									value={speed}
									onChange={(value) => {
										setAttributes({
											speed: value,
										});
									}}
									trackColor='#2184f9'
									min={0.1}
									max={10}
									step={0.1}
								/>
							</PanelBody>
							<PanelBody
								initialOpen={true}
								title={__('Interactivity', 'greenshiftgsap')}
							>
								<div style={{ marginBottom: 20, color: "grey" }}>{__('These options are working only on frontend part')}</div>
								<SelectControl
									label={__('Interactivity mode', 'greenshiftgsap')}
									options={[
										{ value: '', label: __('No', 'greenshiftgsap') },
										{ value: 'scroll', label: __('On scroll', 'greenshiftgsap') },
										{ value: 'cursor', label: __('On mouse interactions', 'greenshiftgsap') },
									]}
									value={mode}
									onChange={(value) => setAttributes({ mode: value })}
								/>
								{mode == "scroll" && (
									<>
										<SelectControl
											label={__('Interactivity actions', 'greenshiftgsap')}
											options={[
												{ value: 'play', label: __('Play', 'greenshiftgsap') },
												{ value: 'seek', label: __('On scroll frame change', 'greenshiftgsap') },
												{ value: 'loop', label: __('Loop play', 'greenshiftgsap') },
											]}
											value={modetypescroll}
											onChange={(value) => setAttributes({ modetypescroll: value })}
										/>
										<RangeControl
											label={__("Visitibility Start", 'greenshiftgsap')}
											value={visibilityStart}
											onChange={(value) => {
												setAttributes({
													visibilityStart: value,
												});
											}}
											trackColor='#2184f9'
											min={0}
											max={0.7}
											step={0.1}
										/>
										<RangeControl
											label={__("Visitibility End", 'greenshiftgsap')}
											value={visibilityEnd}
											onChange={(value) => {
												setAttributes({
													visibilityEnd: value,
												});
											}}
											trackColor='#2184f9'
											min={0.7}
											max={1}
											step={0.1}
										/>
										<RangeControl
											label={__("Frame Start", 'greenshiftgsap')}
											value={frameStart}
											onChange={(value) => {
												setAttributes({
													frameStart: value,
												});
											}}
											trackColor='#2184f9'
											min={0}
											max={500}
											step={1}
										/>
										<RangeControl
											label={__("Frame End", 'greenshiftgsap')}
											value={frameEnd}
											onChange={(value) => {
												setAttributes({
													frameEnd: value,
												});
											}}
											trackColor='#2184f9'
											min={10}
											max={1000}
											step={1}
										/>
									</>
								)

								}
								{mode == "cursor" &&
									<SelectControl
										label={__('Interactivity actions', 'greenshiftgsap')}
										options={[
											{ value: 'hover', label: __('Hover play', 'greenshiftgsap') },
											{ value: 'click', label: __('Click play', 'greenshiftgsap') },
											{ value: 'toggle', label: __('Toggle click play', 'greenshiftgsap') },
											{ value: 'hold', label: __('Reverse play on hover', 'greenshiftgsap') },
											{ value: 'pauseHold', label: __('Reverse play on hover with pause', 'greenshiftgsap') },
										]}
										value={modetypecursor}
										onChange={(value) => setAttributes({ modetypecursor: value })}
									/>
								}

							</PanelBody>

							{ /* Spacing */}
							<PanelBody title={__("Spacing")} initialOpen={false}>
								<Spacing attributeName="spacing" overflow={false} {...props} />
							</PanelBody>

						</InspectorTab>
						<InspectorTab key={'advance'}>
							{ /* Css Transform */}
							<PanelBody title={__("Css Transform")} initialOpen={false}>
								<CssTransform attributeName="csstransform" {...props} />
							</PanelBody>

							{ /* Position Tab */}
							<PanelBody title={__("Position")} initialOpen={false}>
								<Position attributeName="position" {...props} />
							</PanelBody>
							{ /* Responsive */}
							<PanelBody title={__("Responsive")} initialOpen={false}>
								<Responsive attributeName="responsive" {...props} />
							</PanelBody>
						</InspectorTab>
					</InspectorTabs>
				</div>

			</InspectorControls>,

			<BlockControls>
				<AlignmentToolbar
					value={align}
					onChange={(value) => {
						setAttributes({ align: value })
					}}
					alignmentControls={ALIGNMENT_CONTROLS}
				/>
			</BlockControls>,
			<BlockToolBar {...props} />,
			<>
				<div {...blockProps}>
					<lottie-player autoplay loop id={lottieId} src={source} background={background} speed={speed} style={{ width: width[0] + widthUnit[0], height: height[0] + heightUnit[0] }} {...Lottieprops}></lottie-player>
					<style
						dangerouslySetInnerHTML={{
							__html: editor_css,
						}}
					/>
				</div>
			</>
		];
	},

	save(props) {
		const {
			attributes: {
				source, loop, autoplay, background, mode, visibilityStart, visibilityEnd, frameStart, frameEnd, width, height, speed, imageurl, align, modetypescroll, modetypecursor, imagealt, bounce, id, widthUnit, heightUnit, disablelazy, loadnow
			}
		} = props;

		let Lottieprops = {
			loop: (loop && !mode) ? true : null,
			autoplay: (autoplay && !mode) ? true : null,
			mode: (bounce && !mode) ? "bounce" : null,
			"data-visibility-start": visibilityStart ? visibilityStart : '',
			"data-visibility-end": visibilityEnd ? visibilityEnd : '',
			"data-frames-start": frameStart ? frameStart : '',
			"data-frames-end": frameEnd ? frameEnd : '',
			"data-modetype": mode == 'scroll' ? modetypescroll : modetypecursor,
			"data-interactivity": mode ? mode : '',
		}

		const lottieId = `gs-lottie-${id}`;

		const blockProps = useBlockProps.save({
			className: `gs-lottieloader ${lottieId}${loadnow ? ' gs-loadnow': ''}`,
			style: { display: "flex", justifyContent: align },
		});


		return (
			<div {...blockProps}>
				<lottie-player id={lottieId} src={source} background={background} speed={speed} style={{ width: width[0] + widthUnit[0], height: height[0] + heightUnit[0] }} {...Lottieprops}></lottie-player>
				{imageurl &&
					<img
						src={imageurl}
						alt={imagealt}
						width={width[0] + widthUnit[0]}
						height={height[0] + heightUnit[0]}
						style={{ pointerEvents: "none" }}
						loading={disablelazy ? "eager" : null}
					/>
				}
			</div>
		);
	},
});
