import { __ } from '@wordpress/i18n';
import { ToggleControl, TextareaControl, Button} from '@wordpress/components';


const CommonForm = (props) => {
    const {
        attributes: {
            flipstyle,
            flipscale,
            flipabsolute,
            customobject,
            id
        },
        setAttributes,
    } = props;

    function animationEditorReset(animationref) {
        const gsapflip = animationref.querySelector('.gs-flip-wrap');
        if (gsapflip) {
            let stflip = ScrollTrigger.getById('gsflip'+id);
            if(stflip) stflip.kill();
            Flip.killFlipsOf(gsapflip);
            gsapflip.classList.remove('flipactive');
            let customflip = '';
            let $anobj = '';
            if(customobject) {
                if (customobject.indexOf("#") == 0 || customobject.indexOf(".") == 0 ) {
                    let ownerDocument = animationref.ownerDocument;
                    $anobj = ownerDocument.querySelector(customobject);
                } else {
                    $anobj = gsapflip.querySelector(customobject);
                }
                $anobj.classList.remove('flipactive');
            }
        }
    }

    function animationEditorInit(animationref) {
        const gsapflip = animationref.querySelector('.gs-flip-wrap');
        if (gsapflip) {
            let stflip = ScrollTrigger.getById('gsflip'+id);
            if(stflip) stflip.kill();
            Flip.killFlipsOf(gsapflip);
            let ownerDocument = animationref.ownerDocument;
            if(!ownerDocument.body.classList.contains('editor-styles-wrapper')){
                GSFlipinit(gsapflip, true, ownerDocument, id, true);
            }
        }
    }

    return (
        <div className="gs-inspector-form-inspector">
            <div style={{backgroundColor:"#f0f6ff", padding:10, fontSize:12, marginBottom:15}}>
                {__("This module allows to use Flip GSAP plugin and animate from current state to new state by custom trigger. We recommend to test it on Frontend")}
            </div>
            <TextareaControl
                label={__("Custom Css")}
                help={__("Add css styles for Flip state. You must set styles for .flipactive class. Use {GREENSHIFT} to limit styles by this block")}
                value={flipstyle}
                onChange={(value) => {
                    setAttributes({ flipstyle: value });
                }}
            />
            <ToggleControl
                label={__('Add auto scale')}
                checked={flipscale}
                onChange={(value) => {
                    setAttributes({ flipscale: value })
                }}
            />
            <ToggleControl
                label={__('Add absolute position')}
                checked={flipabsolute}
                onChange={(value) => {
                    setAttributes({ flipabsolute: value })
                }}
            />
            <Button style={{marginRight:10}} isSecondary isSmall onClick={() => {
                animationEditorReset(props.animationref);
            }}>{__('Reset in Editor')}</Button>
            <Button isSecondary isSmall onClick={() => {
                animationEditorInit(props.animationref);
            }}>{__('Activate in Editor')}</Button>

        </div>
    );
};

export default CommonForm;