/**
 * Icon box Block
 */

// Import Styles
import './editor.scss';
import Edit from './edit';
import Blob from './components/Blob';

// Import wp dependencies
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;

// Import block dependencies
import attributes from './components/attributes';
import blockIcon from './components/icon';


// Register Block
registerBlockType('greenshift-blocks/blob', {
	title: __('Animated Blob'),
	description: __('Blob maker with animation and mask'),
	icon: blockIcon,
	category: 'Greenshiftpro',
	keywords: [__('blob'), __('greenshift')],
	supports: {
		reusable: true,
		anchor: true,
	},
	example: {},

	// Define attributes
	attributes: attributes,
	edit: Edit,
	save(props) {
		// Save container
		return <Blob {...props} />;
	},
});
