const { __ } = wp.i18n;
import { SelectControl, TextControl, ToggleControl, BaseControl, __experimentalNumberControl as NumberControl } from '@wordpress/components';

const TriggerForm = (props) => {
	const {
		attributes: {
			strandom,
			triggertype,
			customtrigger,
			customobject,
			triggerstart,
			triggerend,
			triggerscrub,
			pinned,
			pinspace,
			pinReparent,
			triggeraction,
			triggersnap,
			batchint,
		},
		setAttributes,
	} = props;

	const TriggerDocLink = () => {
		return (
			<>
				{__('Check documentation for available values', 'greenshiftgsap')} - <a href="https://greensock.com/docs/v3/Plugins/ScrollTrigger" target="__blank"> ScrollTrigger Docs </a>
			</>
		)
	}

	return (
		<div className="gs-inspector-form-inspector">
			<span className="gspb_inspector_property-title">
				{__("Trigger type")}
			</span>
			<SelectControl
				value={triggertype}
				options={[
					{ label: __("Scroll trigger"), value: "scroll" },
					{ label: __("On load"), value: "load" },
					{ label: __("Batch Scroll"), value: "batch" },
					{ label: __("On Hover"), value: "hover" },
					{ label: __("On Click"), value: "click" },
					{ label: __("Toggle Click"), value: "toggleclick" },
				]}
				onChange={(value) => setAttributes({ triggertype: value })}
				help={<TriggerDocLink />}
			/>
			{triggertype !== "load" && (
				<>
					{triggertype === "batch" ? (
						<span className="gspb_inspector_property-title">
							{__("Custom class for Batch scroll")}
						</span>
					) : (
						<span className="gspb_inspector_property-title">
							{__("Selector for custom trigger")}
						</span>
					)
					}
					<TextControl
						value={customtrigger}
						onChange={(value) => setAttributes({ customtrigger: value })}
						help={__(
							"By default, trigger will be block itself, but you can set custom trigger. Use .class or #id of element OR div.class, img, button to search selector inside current container"
						)}
					/>
				</>
			)}
			{triggertype !== "batch" && (
				<>
					<span className="gspb_inspector_property-title">
						{__("Object where to add animation")}
					</span>
					<TextControl
						value={customobject}
						onChange={(value) => setAttributes({ customobject: value })}
						help={__(
							"By default, will be applied to current object but you can apply to custom object. Use .class or #id of element OR div.class, img, button to search selector inside current container"
						)}
					/>
				</>
			)}
			{(triggertype !== "load" && triggertype !== "hover" && triggertype !== "click") && (
				<>
					<>
						<span className="gspb_inspector_property-title">
							{__("Trigger start")}
						</span>
						<TextControl
							value={triggerstart}
							onChange={(value) => setAttributes({ triggerstart: value })}
							help={__(
								"By default, trigger is set to top point of element, but you can change this. Example: top center"
							)}
						/>
					</>
					<>
						<span className="gspb_inspector_property-title">
							{__("Trigger end")}
						</span>
						<TextControl
							value={triggerend}
							onChange={(value) => setAttributes({ triggerend: value })}
							help={__(
								"By default, trigger scroll end is set to bottom point of element, but you can change this. Example: +=300 will set end of trigger as 300px after start"
							)}
						/>
					</>
					{triggertype === "batch" && (
						<>
							<span className="gspb_inspector_property-title">
								{__("Interval between items")}
							</span>
							<NumberControl
								onChange={(value) => setAttributes({ batchint: parseFloat(value) })}
								help={__(
									"By default, interval is 0.15, but you can set other."
								)}
								isDragEnabled
								isShiftStepEnabled
								shiftStep={0.1}
								min={0.01}
								max={3}
								step={0.01}
								value={batchint}
							/>
						</>
					)}
					{triggertype !== "batch" && (
						<>
							<>
								<span className="gspb_inspector_property-title">
									{__("Interpolate animation by Scroll")}
								</span>

								<NumberControl
									onChange={(value) => setAttributes({ triggerscrub: parseFloat(value) })}
									help={__(
										"By default, scroll will trigger full animation. If you want to play animation by scrolling, place here number of seconds for feedback. Better to check how it works on Frontend. Recommended value is 1."
									)}
									isDragEnabled
									isShiftStepEnabled
									shiftStep={1}
									min={0.3}
									max={10}
									step={0.1}
									value={triggerscrub}
								/>
							</>
							<>
								<span className="gspb_inspector_property-title">
									{__("Trigger actions")}
								</span>
								<TextControl
									value={triggeraction}
									onChange={(value) =>
										setAttributes({ triggeraction: value })
									}
									help={__("Determines how the linked animation is controlled at the 4 distinct toggle places - onEnter, onLeave, onEnterBack, and onLeaveBack. Possible values: play, pause, resume, reset, restart, complete, reverse, and none. Default is: play pause resume reset")}
								/>
							</>
							<BaseControl className="gs-label-row" help={__("Formula: 1 / (number of section - 1). Snap are working only on frontend part of site")}>
								<NumberControl
									onChange={(value) => setAttributes({ triggersnap: parseFloat(value) })}
									label={__('Scroll snap')}
									isDragEnabled
									isShiftStepEnabled
									shiftStep={0.1}
									min={0.01}
									max={1}
									step={0.01}
									value={triggersnap}
								/>
							</BaseControl>
						</>
					)}
					{triggertype === "batch" && (
						<div className="gspb_row">
							<div className="gspb_row__col--10" style={{ padding: 0 }}>
								<span className="gspb_inspector_property-title">
									{__("Enable random order")}
								</span>
							</div>
							<div className="gspb_row__col--2" style={{ padding: 0, marginTop: "5px" }}>
								<ToggleControl
									checked={strandom}
									onChange={(value) => setAttributes({ strandom: value })}
								/>
							</div>
						</div>
					)}
				</>
			)}
		</div>
	);
};

export default TriggerForm;