/**
 * Animation Container Block
 */

// Import Styles
import './editor.scss';

// Import wp dependencies
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
import {
	InnerBlocks
} from '@wordpress/block-editor';
const { select } = wp.data;
import { useEffect, useRef, useCallback } from '@wordpress/element';
import { flipicon } from './components/icons.js'

import attributes from './components/attributes';
import AnimationContainer from './components/AnimationContainer';
import Inspector from './components/inspector';


// Import gspb depenedencies
const { gspb_setBlockId } = gspblib.utilities;
const { ExtraToolBar } = gspblib.editor;
const { BlockToolBar } = gspblib.components;

// Register Block
registerBlockType('greenshift-blocks/flipstate', {
    title: __('Flip state Animation'),
    description: __('Adds Flip Animation'),
    category: 'Greenshiftpro',
    keywords: [__('container'), __('animation'), __('effects')],
    icon: flipicon,
    supports: {
        reusable: true,
        anchor: true,
        align: ['wide', 'full'],
    },

    // Define attributes
    attributes: attributes,

    edit(props) {

        // Set Unique Block ID
        gspb_setBlockId(props);

        const {
            attributes: {
                id,
                ease,
                delay,
                duration,
                triggertype,
                customtrigger,
                customobject,
                triggerstart,
                triggerend,
                triggeraction,
                flipscale,
                flipabsolute,
            },
            clientId
        } = props;

        let hasChildBlocks = select('core/block-editor').getBlockOrder(clientId).length > 0;

        const AnimationRef = useRef();
        const isInitialMount = useRef(true);
        const isStillMounted = useRef();
        const debounce = useCallback(
            _.debounce((_atts) => {
                isStillMounted.current = true;
                if (isStillMounted.current) {
                    const gsapflip = AnimationRef.current.querySelector('.gs-flip-wrap');
                    let ownerDocument = AnimationRef.current.ownerDocument;
                    if (gsapflip) {
                        let stflip = ScrollTrigger.getById('gsflip'+id);
                        if(stflip) stflip.kill();
                        Flip.killFlipsOf(gsapflip);
                        if(!ownerDocument.body.classList.contains('editor-styles-wrapper')){
                            GSFlipinit(gsapflip, true, ownerDocument, id);
                        }
                    }
                }
                return () => { isStillMounted.current = false; };
            }, 600),
            []
        );
        useEffect(() => {
            if (isInitialMount.current) {
                isInitialMount.current = false;
            } else {
                debounce(attributes);
            }
        }, [                
            ease,
            delay,
            duration,
            triggertype,
            customtrigger,
            customobject,
            triggerstart,
            triggerend,
            triggeraction,
            flipscale,
            flipabsolute,]);

        return (
            <>
				{hasChildBlocks &&
					<ExtraToolBar
						editOnly={true}
						inner={true}
						gray={true}
						{...props}
					/>
				}
                <Inspector animationref={AnimationRef.current} {...props} />
                <BlockToolBar {...props}/>
                <div className="gs-animation" ref={AnimationRef}>
                    <AnimationContainer editor={true} {...props}>
                    <InnerBlocks
                            renderAppender={ () => hasChildBlocks ? <InnerBlocks.DefaultBlockAppender /> : <InnerBlocks.ButtonBlockAppender /> }
                        />
                    </AnimationContainer>
                </div>
            </>
        );
    },

    save(props) {
        return (
            <AnimationContainer editor={false} {...props}>
                <InnerBlocks.Content />
            </AnimationContainer>
        );
    },
});
