// Import wp dependencies
const { __ } = wp.i18n;

const { InspectorControls, BlockControls, AlignmentToolbar, } = wp.blockEditor;
import animationsTypes from '../../animation-container/components/forms/Animationstype';
import { SelectControl, BaseControl, __experimentalNumberControl as NumberControl, ToggleControl, TextControl } from '@wordpress/components';
const { GsbpFormLabel } = gspblib.components;
const { PanelBody } = wp.components;
const {
	Spacing,
	Responsive,
	CssTransform,
	Position,
	Background,
	Border,
	Shadow
} = gspblib.collections;
const { InspectorTabs, InspectorTab } = gspblib.components;


const Inspector = (props) => {

	const {
		attributes: {
			flexAlign,
			ease,
			delay,
			customtrigger,
			triggerstart,
			triggerend,
			triggerscrub,
			pinspace,
			pinReparent,
			anticipatePin,
		},
		setAttributes,
	} = props;

	const ALIGNMENT_CONTROLS = [
		{
			icon: 'editor-alignleft',
			title: __('Align Text Left'),
			align: 'flex-start',
		},
		{
			icon: 'editor-aligncenter',
			title: __('Align Text Center'),
			align: 'center',
		},
		{
			icon: 'editor-alignright',
			title: __('Align Text Right'),
			align: 'flex-end',
		},
	];

	const TriggerDocLink = () => {
		return (
			<>
				{__('Check documentation for available values', 'greenshiftgsap')} - <a href="https://greensock.com/docs/v3/Plugins/ScrollTrigger" target="__blank"> ScrollTrigger Docs </a>
			</>
		)
	}

	return (
		<>
			<InspectorControls>
				<InspectorTabs tabs={['general', 'advance']}>
					<InspectorTab key={'general'}>
						<PanelBody title={__("Animation options")} initialOpen={true}>
							<div className="gs-inspector-form-inspector">
								<>
									<span className="gspb_inspector_property-title">
										{__("Css ID of custom trigger to start")}
									</span>

									<TextControl
										value={customtrigger}
										onChange={(value) => setAttributes({ customtrigger: value })}
										help={__(
											"By default, animation will be triggered by itself. You can place here custom ID for element"
										)}
									/>
								</>
								<div className="gspb_row">
									<div className="gspb_row__col--7" style={{ padding: 0 }}>
										<GsbpFormLabel
											title={__("Ease type")}
											clearFun={() => setAttributes({ ease: null })}
										/>
									</div>
									<div className="gspb_row__col--5" style={{ padding: 0 }}>
										<SelectControl
											value={ease}
											options={animationsTypes}
											onChange={(value) => setAttributes({ ease: value })}
										/>
									</div>
								</div>
								<BaseControl help={<TriggerDocLink />}>
									<span className="gspb_inspector_property-title">
										{__("Trigger start")}
									</span>
									<TextControl
										value={triggerstart}
										onChange={(value) => setAttributes({ triggerstart: value })}
										help={__(
											"By default, trigger is set to top point of element, but you can change this. Example: top center"
										)}
									/>
								</BaseControl>
								<>
									<span className="gspb_inspector_property-title">
										{__("Trigger end")}
									</span>
									<TextControl
										value={triggerend}
										onChange={(value) => setAttributes({ triggerend: value })}
									/>
								</>
								<>
									<span className="gspb_inspector_property-title">
										{__("Interpolate speed by Scroll")}
									</span>

									<NumberControl
										onChange={(value) => setAttributes({ triggerscrub: parseFloat(value) })}
										help={__(
											"By default, scroll will trigger full animation. If you want to play animation by scrolling, place here number of seconds for feedback.  Recommended value is 1."
										)}
										isDragEnabled
										isShiftStepEnabled
										shiftStep={1}
										min={0.3}
										max={10}
										step={0.1}
										value={triggerscrub}
									/>
								</>
								<div className="gspb_row">
									<div className="gspb_row__col--10" style={{ padding: 0 }}>
										<span className="gspb_inspector_property-title">
											{__("Enable overflow on pinned item")}
										</span>
									</div>
									<div
										className="gspb_row__col--2"
										style={{ marginTop: "5px", padding: 0 }}
									>
										<ToggleControl
											checked={pinspace}
											onChange={(value) => setAttributes({ pinspace: value })}
										/>
									</div>
								</div>
								<div className="gspb_row">
									<div className="gspb_row__col--10" style={{ padding: 0 }}>
										<span className="gspb_inspector_property-title">
											{__("Pin Reparent. Use this if you have issues with pin")}
										</span>
									</div>
									<div
										className="gspb_row__col--2"
										style={{ marginTop: "5px", padding: 0 }}
									>
										<ToggleControl
											checked={pinReparent}
											onChange={(value) => setAttributes({ pinReparent: value })}
										/>
									</div>
								</div>
								<div className="gspb_row">
									<div className="gspb_row__col--10" style={{ padding: 0 }}>
										<span className="gspb_inspector_property-title">
											{__("Anticipate pin. Use this only if have glitch on pin start")}
										</span>
									</div>
									<div
										className="gspb_row__col--2"
										style={{ marginTop: "5px", padding: 0 }}
									>
										<ToggleControl
											checked={anticipatePin}
											onChange={(value) => setAttributes({ anticipatePin: value })}
										/>
									</div>
								</div>

								<BaseControl className="gs-label-row">
									<NumberControl
										onChange={(value) => setAttributes({ delay: parseFloat(value) })}
										label={__('Delay')}
										isDragEnabled
										isShiftStepEnabled
										shiftStep={1}
										min={0.1}
										max={20}
										step={0.1}
										value={delay}
									/>
								</BaseControl>

							</div>
						</PanelBody>
						{ /* Background Settings */}
						<PanelBody
							title={__("Background and Opacity", 'greenshift')}
							initialOpen={false}
							className="gspb_inspector gspb_smallpadding_btn"
						>
							<Background
								attributeName="background"
								exclude={['video']}
								{...props}
							/>
						</PanelBody>
						{ /* Border Settings */}
						<PanelBody title={__("Border", 'greenshift')} initialOpen={false}>
							<Border attributeName="border" {...props} />
						</PanelBody>
						{ /* Shadow Settings */}
						<PanelBody title={__("Shadow", 'greenshift')} initialOpen={false}>
							<Shadow attributeName="shadow" {...props} predefined={true} />
						</PanelBody>
					</InspectorTab>
					<InspectorTab key={'advance'}>

						{ /* Css Transform */}
						<PanelBody title={__("Css Transform")} initialOpen={false}>
							<CssTransform attributeName="csstransform" {...props} />
						</PanelBody>

						{ /* Position Tab */}
						<PanelBody title={__("Position")} initialOpen={false}>
							<Position attributeName="position" {...props} />
						</PanelBody>
						{ /* Responsive */}
						<PanelBody title={__("Responsive")} initialOpen={false}>
							<Responsive attributeName="responsive" {...props} />
						</PanelBody>
						{ /* Spacing */}
						<PanelBody title={__("Spacing")} initialOpen={false}>
							<Spacing attributeName="spacing" autoMargin={true} {...props} />
						</PanelBody>
					</InspectorTab>
				</InspectorTabs>
			</InspectorControls>
			<BlockControls>
				<AlignmentToolbar
					value={flexAlign}
					onChange={value => setAttributes({ flexAlign: value })}
					alignmentControls={ALIGNMENT_CONTROLS}
				/>
			</BlockControls>
		</>
	);
}
export default Inspector;