const RevealWrapper = ({ child, props }) => {
    const {
        attributes: {
            reveal_dir,
            reveal_speed,
            reveal_delay,
            reveal_bg,
            reveal_enabled
        },
        editor
    } = props;

    return (
        <div className="gs-reveal-wrap" style={{ position: 'relative', display: "inline-block" }}>
            <div className="gs-reveal-cont">
                {child}
            </div>
            {reveal_enabled && (
                <div
                    data-reveal-dir={reveal_dir}
                    data-reveal-speed={reveal_speed}
                    data-reveal-delay={reveal_delay}
                    data-reveal-bg={reveal_bg}
                    className="gs-reveal-block"
                    style={{
                        position: 'absolute',
                        top: 0,
                        right: 0,
                        bottom: 0,
                        left: 0,
                        width: '100%',
                        pointerEvents: 'none'
                    }}
                ></div>
            )}
        </div>
    );
}

export default RevealWrapper;