const {
    gspb_spacing_cssGen,
    gspb_responsive_cssGen,
    gspb_position_cssGen,
    gspb_typography_cssGen,
    gspb_background_cssGen,
    gspb_shadow_cssGen,
    gspb_border_cssGen,
    gspb_Css_Final
} = gspblib.utilities;
const { gspb_cssGen } = gspblib.helpers;

export const AnimationContainer = (props) => {

    const {
        attributes: {
            id,
            responsive,
            position,
            spacing,
            filterarray,
            showAll,
            allLabel,
            flexAlign,
            gap,
            spaceFilter,
            checkboxColor,
            disableCheckbox,
            shadowItem,
            shadowChecked,
            borderItem,
            borderChecked,
            backgroundItem,
            backgroundChecked,
            spacingItem,
            typographyItem,
            typographyChecked,
            minWidth
        },
        children,
        clientId,
        className
    } = props;

    let containerId = `gspb_flipfilter-${id}`;
    let css_selector_by_user = '#' + containerId;
    let final_css = '';

    final_css += `.gspb-checkbox-item{position:absolute;z-index:-1;opacity:0}.gspb-checkbox-item+label{display:inline-flex;align-items:center;user-select:none}`;
    if (!disableCheckbox) {
        final_css += `.gspb-checkbox-item+label::before{content:'';display:inline-block;width:1em;height:1em;flex-shrink:0;flex-grow:0;border:1px solid #adb5bd;border-radius:.25em;margin-right:.5em;background-repeat:no-repeat;background-position:center center;background-size:50% 50%}.gspb-checkbox-item:not(:disabled):not(:checked)+label:hover::before{border-color:#d9dde2;opacity:1}.gspb-checkbox-item:not(:disabled):active+label::before{background-color:#d9dde2;border-color:#d9dde2}${css_selector_by_user} .gspb-checkbox-item:focus:not(:checked)+label::before{border-color:${checkboxColor};opacity:0.5}${css_selector_by_user} .gspb-checkbox-item:checked+label::before{border-color:${checkboxColor};background-color:${checkboxColor};background-image:url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e")}.gspb-checkbox-item:disabled+label::before{background-color:#e9ecef}`;
    }

    // Typography
    final_css = gspb_typography_cssGen(
        typographyItem,
        css_selector_by_user + ' .gspb-checkbox-item+label',
        final_css
    );
    final_css = gspb_typography_cssGen(
        typographyChecked,
        css_selector_by_user + ' .gspb-checkbox-item:checked+label',
        final_css
    );

    final_css = gspb_spacing_cssGen(
        spacingItem,
        css_selector_by_user + ' .gspb-checkbox-item+label',
        final_css
    );

    // Border
    final_css = gspb_border_cssGen(
        borderItem,
        css_selector_by_user + ' .gspb-checkbox-item+label',
        final_css
    );
    final_css = gspb_border_cssGen(
        borderChecked,
        css_selector_by_user + ' .gspb-checkbox-item:checked+label',
        final_css
    );

    // Background
    final_css = gspb_background_cssGen(
        backgroundItem,
        css_selector_by_user + ' .gspb-checkbox-item+label',
        final_css
    );
    final_css = gspb_background_cssGen(
        backgroundChecked,
        css_selector_by_user + ' .gspb-checkbox-item:checked+label',
        final_css
    );

    // Shadow
    final_css = gspb_shadow_cssGen(
        shadowItem,
        css_selector_by_user + ' .gspb-checkbox-item+label',
        final_css
    );
    final_css = gspb_shadow_cssGen(
        shadowChecked,
        css_selector_by_user + ' .gspb-checkbox-item:checked+label',
        final_css
    );

    // Responsive classes
    final_css = gspb_responsive_cssGen(
        responsive,
        css_selector_by_user,
        final_css,
    );

    // Spacing
    final_css = gspb_spacing_cssGen(
        spacing,
        css_selector_by_user,
        final_css
    );

    final_css = gspb_cssGen(
        `${css_selector_by_user} .gspb-flipfilters-wrap`,
        ['display', 'justify-content', 'gap', 'align-items', 'margin-bottom', 'flex-wrap'],
        [
            ["flex"],
            [flexAlign],
            [gap + "px"],
            ["center"],
            [spaceFilter + "px"],
            ['wrap']

        ],
        final_css,
        false
    );

    final_css = gspb_cssGen(
        css_selector_by_user + ' .gspb-checkbox-item+label',
        ['min-width', 'justify-content'],
        [
            [minWidth ? minWidth+'px' : null],
            [minWidth ? 'center' : null],

        ],
        final_css,
        false
    );

    // Get The Stored CSS
    gspb_Css_Final(id, final_css, props);
    let editor_css = final_css;

    // Position
    editor_css = gspb_position_cssGen(
        position,
        '#block-' + clientId,
        editor_css,
    );

    return (
        <>
            <div
                id={containerId}
                className={`gspb-flipfilters${className ? ' ' + className : ''}`}
            >
                {children}
            </div>

            {props.editor ? (
                <style
                    dangerouslySetInnerHTML={{
                        __html: editor_css,
                    }}
                />
            ) : null}
        </>
    );
};

export default AnimationContainer;
