/**
 * Animation Container Block
 */

// Import Styles
import './editor.scss';

// Import wp dependencies
const { __ } = wp.i18n;
const { registerBlockType } = wp.blocks;
import {
    InnerBlocks
} from '@wordpress/block-editor';
const { select } = wp.data;
import { useEffect, useRef, useState } from '@wordpress/element';
import { flipicon } from './components/icons.js'

import attributes from './components/attributes';
import AnimationContainer from './components/AnimationContainer';
import Inspector from './components/inspector';


// Import gspb depenedencies
const { gspb_setBlockId } = gspblib.utilities;
const { BlockToolBar } = gspblib.components;

// Register Block
registerBlockType('greenshift-blocks/flipfilter', {
    title: __('Filterable container'),
    description: __('Adds custom filters to items'),
    category: 'Greenshiftpro',
    keywords: [__('filter'), __('gallery'), __('effects')],
    icon: flipicon,
    supports: {
        reusable: true,
        anchor: true,
        align: ['wide'],
    },

    // Define attributes
    attributes: attributes,

    edit(props) {

        // Set Unique Block ID
        gspb_setBlockId(props);

        const {
            attributes: {
                id,
                filterarray,
                showAll,
                allLabel,
                checkedState,
                reinit
            },
            clientId,
            setAttributes
        } = props;

        let hasChildBlocks = select('core/block-editor').getBlockOrder(clientId).length > 0;

        function updateFilters(items, filters, allCheckbox) {
            const state = Flip.getState(items),
                classes = filters.filter(checkbox => checkbox.checked).map(checkbox => "." + checkbox.id),
                matches = classes.length ? gsap.utils.toArray(AnimationRef.current.querySelectorAll(classes.join(","))) : classes; 

            items.forEach(item => {
                if (matches.indexOf(item) === -1) {
                    item.style.display = "none";
                    item.classList.add('filter-remove-item');
                } else {
                    item.classList.remove('filter-remove-item');
                    item.style.display = "";
                }
            });
            Flip.from(state, {
                duration: 0.7,
                scale: true,
                ease: "power1.inOut",
                stagger: 0.08,
                onEnter: elements => gsap.fromTo(elements, { opacity: 0, scale: 0 }, { opacity: 1, scale: 1, duration: 1 }),
                onLeave: elements => gsap.to(elements, { opacity: 0, scale: 0, duration: 1 })
            });
            //allCheckbox.checked = matches.length === items.length;
        }

        const AnimationRef = useRef();
        useEffect(() => {
            if (AnimationRef.current) {

                const allCheckbox = AnimationRef.current.querySelector('.gspb-checkbox-filter-all'),
                    filters = gsap.utils.toArray(AnimationRef.current.querySelectorAll('.gspb-checkbox-filter-item')),
                    items = gsap.utils.toArray(AnimationRef.current.querySelectorAll('.gs-flipfilter'));

                filters.forEach(btn => btn.addEventListener('click', function (ev) {
                    updateFilters(items, filters, allCheckbox);
                }
                ));
                allCheckbox.addEventListener('click', function (ev) {
                    filters.forEach(checkbox => checkbox.checked = allCheckbox.checked);
                    updateFilters(items, filters, allCheckbox);

                });
            }
        }, [reinit]);

        const [isChecked, setIsChecked] = useState(true);

        const handleOnChange = (position) => {
            const updatedCheckedState = checkedState.map((item, index) =>
                index === position ? !item : item
            );
            setAttributes({checkedState: updatedCheckedState});
        };

        const handleOnChangeAll = () => {
            setIsChecked(!isChecked);
            const updatedCheckedState = checkedState.map((item, index) =>
            !isChecked
        );
        setAttributes({checkedState: updatedCheckedState});
        };

        return (
            <>
                <Inspector {...props} />
                <BlockToolBar {...props} />
                <div className="gs-animation" ref={AnimationRef}>
                    <AnimationContainer editor={true} {...props}>
                        <div className="gspb-flipfilters-wrap">
                            {showAll &&
                                <div className="gspb-checkbox-filter">
                                    <input className="gspb-checkbox-item gspb-checkbox-filter-all" type="checkbox" id={`allfilter-${id}`} checked={isChecked} onChange={() => {
                                        handleOnChangeAll();
                                    }} />
                                    <label for={`allfilter-${id}`}>{allLabel}</label>
                                </div>
                            }

                            {filterarray.map((filter, index) => {
                                const { label, id } = filter;
                                return (
                                    <>
                                        <div className="gspb-checkbox-filter">
                                            <input className="gspb-checkbox-item gspb-checkbox-filter-item" type="checkbox" id={`${id}`} 
                                                checked={checkedState[index]} 
                                                onChange={() => {
                                                    handleOnChange(index);
                                                }} />
                                            <label for={`${id}`}>{label}</label>
                                        </div>
                                    </>
                                );
                            })}
                        </div>
                        <InnerBlocks
                            renderAppender={() => hasChildBlocks ? <InnerBlocks.DefaultBlockAppender /> : <InnerBlocks.ButtonBlockAppender />}
                        />
                    </AnimationContainer>
                </div>
            </>
        );
    },

    save(props) {
        const {
            attributes: {
                id,
                filterarray,
                showAll,
                allLabel,
            }
        } = props;
        return (
            <AnimationContainer editor={false} {...props}>
                <div className="gspb-flipfilters-wrap">
                    {showAll &&
                        <div className="gspb-checkbox-filter">
                            <input className="gspb-checkbox-item gspb-checkbox-filter-all" type="checkbox" id={`allfilter-${id}`} checked />
                            <label for={`allfilter-${id}`}>{allLabel}</label>
                        </div>
                    }

                    {filterarray.map((filter, index) => {
                        const { label, id } = filter;
                        return (
                            <>
                                <div className="gspb-checkbox-filter">
                                    <input className="gspb-checkbox-item gspb-checkbox-filter-item" type="checkbox" id={`${id}`} checked />
                                    <label for={`${id}`}>{label}</label>
                                </div>
                            </>
                        );
                    })}
                </div>
                <InnerBlocks.Content />
            </AnimationContainer>
        );
    },
});
