const {
    gspb_spacing_cssGen,
    gspb_responsive_cssGen,
    gspb_position_cssGen,
    gspb_csstransform_cssGen,
    gspb_Css_Final,
    gspb_background_cssGen,
    gspb_shadow_cssGen,
    gspb_border_cssGen,
} = gspblib.utilities;
const { gspb_cssGen } = gspblib.helpers;

export const PinScroll = (props) => {

    const {
        attributes: {
            id,
            responsive,
            position,
            csstransform,
            spacing,
            background,
            border,
            shadow,
            flexAlign,
            ease,
            delay,
            customtrigger,
            triggerstart,
            triggerend,
            triggerscrub,
            pinspace,
            pinReparent,
            anticipatePin,
        },
        className,
        clientId,
        children
    } = props;


    let pinScrollId = `gspb_gsapin-${id}`;
    let pinScrollClassName = `gs-pin-wrap gs-gsap-wrap ${className}`;
    let css_selector_by_user = '#' + pinScrollId;
    let final_css;

    // Responsive classes
    final_css = gspb_responsive_cssGen(
        responsive,
        css_selector_by_user,
        final_css,
    );

    // Position
    final_css = gspb_position_cssGen(
        position,
        'body.gspb-bodyfront ' + css_selector_by_user,
        final_css,
    );

    // Border
    final_css = gspb_border_cssGen(
        border,
        css_selector_by_user,
        final_css
    );

    // Background
    final_css = gspb_background_cssGen(
        background,
        css_selector_by_user,
        final_css
    );

    // Shadow
    final_css = gspb_shadow_cssGen(
        shadow,
        css_selector_by_user,
        final_css
    );

    // Css transform
    final_css = gspb_csstransform_cssGen(
        csstransform,
        css_selector_by_user,
        final_css,
        css_selector_by_user
    );

    // Spacing
    final_css = gspb_spacing_cssGen(
        spacing,
        css_selector_by_user,
        final_css
    );

    // Align
    final_css = gspb_cssGen(
        css_selector_by_user,
        ['justify-content'],
        [flexAlign],
        final_css
    );

    // Get The Stored CSS
    gspb_Css_Final(id, final_css, props);
    let editor_css = final_css;
    // Position
    editor_css = gspb_position_cssGen(
        position,
        '#block-' + clientId,
        editor_css,
    );


    return (
        <div
            id={pinScrollId}
            className={pinScrollClassName}
            data-delay={(delay && Number.isFinite(delay)) ? delay : null}
            data-triggertype="scroll"
            data-customtrigger={customtrigger ? customtrigger : null}
            data-triggerstart={triggerstart ? triggerstart : null}
            data-triggerend={triggerend ? triggerend : null}
            data-ease={ease ? ease : null}
            data-triggerscrub={triggerscrub ? triggerscrub : null}
            data-pinned="yes"
            data-pinforce="yes"
            data-pinreparent={pinReparent ? 'yes' : null}
            data-pinspace={pinspace ? 'yes' : null}
            data-anticipatepin={anticipatePin ? 'yes' : null}
            style={{ display: 'flex' }}
        >

            {children}

            {props.editor ? (
                <style
                    dangerouslySetInnerHTML={{
                        __html: editor_css,
                    }}
                />
            ) : null}
        </div>
    );
};

export default PinScroll;
