/**
 *
 * Image Component.
 *
 */

// Import blpge dependencies
const {
	gspb_spacing_cssGen,
	aos_animation_cssGen,
	gspb_position_cssGen,
	gspb_csstransform_cssGen,
	gspb_responsive_cssGen,
	gspb_Css_Final
} = gspblib.utilities;
const { gspb_cssGen } = gspblib.helpers;
const { AnimationRenderProps } = gspblib.collections;

const Blob = (props) => {

	const {
		attributes: {
			id,
			inlineCssStyles,
			spacing,
			animation,
			responsive,
			position,
			csstransform,
			align,
			width,
			height,
			imagewidth,
			imagewidthUnit,
			widthUnit,
			heightUnit,
			originalWidth,
			originalHeight,
			filltype,
			fillone,
			filltwo,
			offsetone,
			offsettwo,
			gradientRotate,
			stroke,
			strokewidth,
			imageurl,
			image_y,
			image_x,
			tensionPoints,
			minRadius,
			maxRadius,
			minDuration,
			maxDuration,
			numPoints
		},
		className,
		clientId
	} = props;


	let blobId = `gspb_blob-id-${id}`;

	// for styles applied by the user
	let css_selector_by_user = `#${blobId}`;

	// we should keep className generated by Gutenberg
	let blobClassName = `gspb_blob ${blobId} ${className}`;

	// Final CSS for the block
	let final_css;

	// Align
	final_css = gspb_cssGen(
		css_selector_by_user,
		['text-align'],
		[
			[
				[align[0] ? align[0] : null],
				[align[1] ? align[1] : null],
				[align[2] ? align[2] : null],
				[align[3] ? align[3] : null],
			],
		],
		final_css
	);



	// Spacing
	final_css = gspb_spacing_cssGen(
		spacing,
		css_selector_by_user,
		final_css
	);

	// Animation
	final_css = aos_animation_cssGen(
		animation,
		css_selector_by_user,
		final_css
	);

	// Responsive classes
	final_css = gspb_responsive_cssGen(
		responsive,
		css_selector_by_user,
		final_css,
	);

	// Position
	final_css = gspb_position_cssGen(
		position,
		'body.gspb-bodyfront ' + css_selector_by_user,
		final_css,
	);

	// Css transform
	final_css = gspb_csstransform_cssGen(
		csstransform,
		css_selector_by_user,
		final_css,
		animation
	);

	final_css = gspb_cssGen(
		css_selector_by_user + ' svg',
		['width'],
		[
			[
				[width[0] ? width[0] + widthUnit[0] : null],
				[width[1] ? width[1] + widthUnit[1] : null],
				[width[2] ? width[2] + widthUnit[2] : null],
				[width[3] ? width[3] + widthUnit[3] : null],
			],
		],
		final_css,
		false
	);

	final_css = gspb_cssGen(
		css_selector_by_user + ' svg',
		['height'],
		[
			[
				[height[0] ? height[0] + heightUnit[0] : null],
				[height[1] ? height[1] + heightUnit[1] : null],
				[height[2] ? height[2] + heightUnit[2] : null],
				[height[3] ? height[3] + heightUnit[3] : null],
			],
		],
		final_css,
		false
	);

	final_css += `${css_selector_by_user} svg{max-width:100%}`;

	//Render Animation Properties
	let AnimationProps = {};
	AnimationProps = AnimationRenderProps(animation);

	// Get The Stored CSS
	gspb_Css_Final(id, final_css, props);
	let editor_css = final_css;

	// Position
	editor_css = gspb_position_cssGen(
		position,
		'#block-' + clientId,
		editor_css,
	);

	let realHeight = '';
	if (originalHeight && originalWidth) {
		let imageProportion = originalHeight / originalWidth;
		realHeight = Math.round(imagewidth * imageProportion);
	}

	let fill = `url(#fill-${id})`;
	if (filltype == 'color' && !filltwo) {
		fill = fillone;
	}

	return (
		<>
			<div
				id={blobId}
				className={blobClassName}
				data-resize={realHeight}
				data-numpoints={numPoints}
				data-minradius={minRadius}
				data-maxradius={minRadius + maxRadius}
				data-minduration={minDuration}
				data-maxduration={minDuration + maxDuration}
				data-tensionpoints={tensionPoints}
				data-id={id}
				{...AnimationProps}
			>
				<svg class="gs-svg-blob" version="1.1" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 600 600" preserveAspectRatio="xMidYMid meet" fill={fill} strokeWidth={strokewidth} stroke={stroke}>
					<path id={`blobpath-${id}`}></path>
					<defs>
						{(filltwo && filltype == 'color') && (
							<linearGradient id={`fill-${id}`} gradientTransform={`rotate(${gradientRotate ? gradientRotate : 0})`}>
								<stop stopColor={fillone ? fillone : "#7d8dff"} offset={`${offsetone ? offsetone + '%' : '0%'}`} />
								<stop stopColor={filltwo} offset={`${offsettwo ? offsettwo + '%' : '100%'}`} />
							</linearGradient>
						)}
						{(imageurl && realHeight && filltype == 'image') && (
							<pattern
								id={`fill-${id}`}
								patternUnits="userSpaceOnUse"
								patternContentUnits="userSpaceOnUse"
								width={`${imagewidth}${imagewidthUnit}`}
								height={`${realHeight}${imagewidthUnit}`}
								x={`${image_x}px`}
								y={`${image_y}px`}
							>
								<image
									id={`imgpattern-${id}`}
									href={imageurl}
									width={`${imagewidth}${imagewidthUnit}`}
									height={`${realHeight}${imagewidthUnit}`}
								>
								</image>
							</pattern>
						)}
					</defs>
				</svg>


			</div>

			{props.editor ? (
				<style
					dangerouslySetInnerHTML={{
						__html: editor_css,
					}}
				/>
			) : null}
		</>
	);

}

export default Blob;