import { __ } from '@wordpress/i18n';
import { useState } from '@wordpress/element';
import { Button, RangeControl } from '@wordpress/components';
const { MediaUpload, MediaUploadCheck } = wp.blockEditor;
const { CssUnits, Devices, StylesforBlock } = gspblib.components;
const { gspb_getDeviceStateIndex } = gspblib.utilities;

const CommonForm = (props) => {
    const {
        attributes: {
            images,
            height,
            width,
            widthUnit,
            heightUnit,
            imageFit,
            widthImage,
            heightImage,
            widthUnitImage,
            heightUnitImage
        },
        setAttributes,
    } = props;

    function animationEditorReset(animationref) {

    }

    const onSelectMedia = (media) => {
        setAttributes({
            images: media,
        });
    };

    const deviceStateIndex = gspb_getDeviceStateIndex();
    const [devstate, setdevState] = useState(0);

    return (
        <div className="gspb_inspector">
            <div style={{ backgroundColor: "#f0f6ff", padding: 10, fontSize: 12, marginBottom: 15 }}>
                {__("This module allows to animate image sequence on scroll or other triggers. Upload image sequence below. It may work with some delay in Editor, so we recommend to test it on frontend part of site", 'greenshiftgsap')}
            </div>

            <MediaUploadCheck>
                <MediaUpload
                    multiple={true}
                    onSelect={(media) => onSelectMedia(media)}
                    allowedTypes={['image']}
                    accept="image/*"
                    value={images.map(item => item.id)}
                    render={({ open }) => {
                        return (
                            <>
                                <Button
                                    isPrimary={true}
                                    onClick={(event) => {
                                        event.stopPropagation();
                                        open();
                                    }}
                                >
                                    {images.length > 0 ? __('Replace Images') : __('Select Images')}
                                </Button>
                            </>
                        );
                    }}
                />
            </MediaUploadCheck>
            <div style={{ marginTop: 10 }} className="gs-gallery-uploader">
                {images.map((item, index) => {
                    return (
                        <img
                            key={item.id}
                            src={item.url}
                            alt={item.alt}
                        />
                    );
                })}

            </div>

            <hr />
            <Devices
                onChange={() =>
                    setdevState(!devstate)
                }
            />
            <div className="gspb_inspector_options-set" style={{ marginBottom: 15 }}>
                <div className="gspb_row gspb_row--no-padding-col">
                    <div className="gspb_row__col--7">
                        <span className="gspb_inspector_property-title">
                            {__('Block Width')}
                        </span>
                    </div>
                    <div
                        className="gspb_row__col--5"
                        style={{
                            justifyContent: 'space-between',
                        }}
                    >
                        <div>

                            <CssUnits
                                units={['px', '%', 'vw']}
                                attribute={
                                    widthUnit[deviceStateIndex] ==
                                        null
                                        ? ''
                                        : widthUnit[deviceStateIndex]
                                }
                                onChange={(value) => {
                                    let currentValue = widthUnit.slice();
                                    currentValue[
                                        deviceStateIndex
                                    ] = value;
                                    setAttributes({
                                        widthUnit: currentValue,
                                    });
                                }}
                            />
                            <div
                                className="gspb_inspector_clear_btn--right"
                                onClick={() => {
                                    let currentValue = width.slice();
                                    currentValue[
                                        deviceStateIndex
                                    ] = null;
                                    setAttributes({
                                        width: currentValue,
                                    });
                                }}
                            >
                                <i className="rhicon rhi-undo" />
                            </div>
                        </div>
                    </div>
                    <div style={{ width: '100%' }}>
                        <RangeControl
                            value={
                                width[deviceStateIndex] == null
                                    ? ''
                                    : width[deviceStateIndex]
                            }
                            onChange={(value) => {
                                let currentValue = width.slice();
                                currentValue[
                                    deviceStateIndex
                                ] = value;
                                setAttributes({
                                    width: currentValue,
                                });
                            }}
                            trackColor='#2184f9'
                            min={
                                widthUnit[deviceStateIndex] ==
                                    'px'
                                    ? 100
                                    : 1
                            }
                            max={
                                widthUnit[deviceStateIndex] ==
                                    'px'
                                    ? 2500
                                    : 100
                            }
                        />
                    </div>
                </div>
                <div className="gspb_row gspb_row--no-padding-col">
                    <div className="gspb_row__col--7">
                        <span className="gspb_inspector_property-title">
                            {__('Block Height')}
                        </span>
                    </div>
                    <div
                        className="gspb_row__col--5"
                        style={{
                            justifyContent: 'space-between',
                        }}
                    >
                        <div>

                            <CssUnits
                                units={['px', '%', 'vh']}
                                attribute={
                                    heightUnit[deviceStateIndex] ==
                                        null
                                        ? ''
                                        : heightUnit[deviceStateIndex]
                                }
                                onChange={(value) => {
                                    let currentValue = heightUnit.slice();
                                    currentValue[
                                        deviceStateIndex
                                    ] = value;
                                    setAttributes({
                                        heightUnit: currentValue,
                                    });
                                }}
                            />
                            <div
                                className="gspb_inspector_clear_btn--right"
                                onClick={() => {
                                    let currentValue = height.slice();
                                    currentValue[
                                        deviceStateIndex
                                    ] = null;
                                    setAttributes({
                                        height: currentValue,
                                    });
                                }}
                            >
                                <i className="rhicon rhi-undo" />
                            </div>
                        </div>
                    </div>
                    <div style={{ width: '100%' }}>
                        <RangeControl
                            value={
                                height[deviceStateIndex] == null
                                    ? ''
                                    : height[deviceStateIndex]
                            }
                            onChange={(value) => {
                                let currentValue = height.slice();
                                currentValue[
                                    deviceStateIndex
                                ] = value;
                                setAttributes({
                                    height: currentValue,
                                });
                            }}
                            trackColor='#2184f9'
                            min={
                                heightUnit[deviceStateIndex] ==
                                    'px'
                                    ? 100
                                    : 1
                            }
                            max={
                                heightUnit[deviceStateIndex] ==
                                    'px'
                                    ? 2500
                                    : 100
                            }
                        />
                    </div>
                </div>
            </div>
            <div className="gspb_inspector_options-set" style={{ marginBottom: 15 }}>
                <div className="gspb_row gspb_row--no-padding-col">
                    <div className="gspb_row__col--7">
                        <span className="gspb_inspector_property-title">
                            {__('Image Width')}
                        </span>
                    </div>
                    <div
                        className="gspb_row__col--5"
                        style={{
                            justifyContent: 'space-between',
                        }}
                    >
                        <div>

                            <CssUnits
                                units={['px', '%', 'vw']}
                                attribute={
                                    widthUnitImage[deviceStateIndex] ==
                                        null
                                        ? ''
                                        : widthUnitImage[deviceStateIndex]
                                }
                                onChange={(value) => {
                                    let currentValue = widthUnitImage.slice();
                                    currentValue[
                                        deviceStateIndex
                                    ] = value;
                                    setAttributes({
                                        widthUnitImage: currentValue,
                                    });
                                }}
                            />
                            <div
                                className="gspb_inspector_clear_btn--right"
                                onClick={() => {
                                    let currentValue = width.slice();
                                    currentValue[
                                        deviceStateIndex
                                    ] = null;
                                    setAttributes({
                                        widthImage: currentValue,
                                    });
                                }}
                            >
                                <i className="rhicon rhi-undo" />
                            </div>
                        </div>
                    </div>
                    <div style={{ width: '100%' }}>
                        <RangeControl
                            value={
                                widthImage[deviceStateIndex] == null
                                    ? ''
                                    : widthImage[deviceStateIndex]
                            }
                            onChange={(value) => {
                                let currentValue = widthImage.slice();
                                currentValue[
                                    deviceStateIndex
                                ] = value;
                                setAttributes({
                                    widthImage: currentValue,
                                });
                            }}
                            trackColor='#2184f9'
                            min={
                                widthUnitImage[deviceStateIndex] ==
                                    'px'
                                    ? 100
                                    : 1
                            }
                            max={
                                widthUnitImage[deviceStateIndex] ==
                                    'px'
                                    ? 2500
                                    : 100
                            }
                        />
                    </div>
                </div>
                <div className="gspb_row gspb_row--no-padding-col">
                    <div className="gspb_row__col--7">
                        <span className="gspb_inspector_property-title">
                            {__('Image Height')}
                        </span>
                    </div>
                    <div
                        className="gspb_row__col--5"
                        style={{
                            justifyContent: 'space-between',
                        }}
                    >
                        <div>

                            <CssUnits
                                units={['px', '%', 'vh']}
                                attribute={
                                    heightUnitImage[deviceStateIndex] ==
                                        null
                                        ? ''
                                        : heightUnitImage[deviceStateIndex]
                                }
                                onChange={(value) => {
                                    let currentValue = heightUnitImage.slice();
                                    currentValue[
                                        deviceStateIndex
                                    ] = value;
                                    setAttributes({
                                        heightUnitImage: currentValue,
                                    });
                                }}
                            />
                            <div
                                className="gspb_inspector_clear_btn--right"
                                onClick={() => {
                                    let currentValue = heightImage.slice();
                                    currentValue[
                                        deviceStateIndex
                                    ] = null;
                                    setAttributes({
                                        heightImage: currentValue,
                                    });
                                }}
                            >
                                <i className="rhicon rhi-undo" />
                            </div>
                        </div>
                    </div>
                    <div style={{ width: '100%' }}>
                        <RangeControl
                            value={
                                heightImage[deviceStateIndex] == null
                                    ? ''
                                    : heightImage[deviceStateIndex]
                            }
                            onChange={(value) => {
                                let currentValue = heightImage.slice();
                                currentValue[
                                    deviceStateIndex
                                ] = value;
                                setAttributes({
                                    heightImage: currentValue,
                                });
                            }}
                            trackColor='#2184f9'
                            min={
                                heightUnitImage[deviceStateIndex] ==
                                    'px'
                                    ? 100
                                    : 1
                            }
                            max={
                                heightUnitImage[deviceStateIndex] ==
                                    'px'
                                    ? 2500
                                    : 100
                            }
                        />
                    </div>
                </div>
            </div>
            <div>{__('Select Image Fill type', 'greenshiftgsap')}</div>
            <StylesforBlock columns={3} value={imageFit} onChange={val => setAttributes({ imageFit: val })}
                options={[
                    { value: 'inherit', label: __('Default', 'greenshiftgsap') },
                    { value: 'cover', label: __('Cover', 'greenshiftgsap') },
                    { value: 'fill', label: __('Fill', 'greenshiftgsap') },
                    { value: 'contain', label: __('Contain', 'greenshiftgsap') },
                    { value: 'none', label: __('None', 'greenshiftgsap') },
                    { value: 'scale-down', label: __('Scale', 'greenshiftgsap') },
                ]}
            />
        </div>
    );
};

export default CommonForm;