const { __ } = wp.i18n;
import { SelectControl, ToggleControl, BaseControl, __experimentalNumberControl as NumberControl } from '@wordpress/components';
const { GsbpFormLabel, BlpgeColorPicker } = gspblib.components;

const RevealForm = (props) => {
    const {
        attributes: {
            reveal_enabled,
            reveal_dir,
            reveal_speed,
            reveal_delay,
            reveal_bg,
        },
        setAttributes,
    } = props;

    return (
        <div className="gs-inspector-form-inspector">
            <>
                <div className="gspb_row">
                    <div className="gspb_row__col--10">
                        <span className="gspb_inspector_property-title">
                            {__("Enable reveal")}
                        </span>
                    </div>
                    <div className="gspb_row__col--2" style={{ marginTop: "5px" }}>
                        <ToggleControl
                            checked={reveal_enabled}
                            onChange={(value) => setAttributes({ reveal_enabled: value })}
                        />
                    </div>
                </div>
            </>
            {reveal_enabled &&
                <>
                    <div className="gspb_row">

                        <div className="gspb_row__col--6">
                            <GsbpFormLabel
                                title={__("Direction: ")}
                                clearFun={() => setAttributes({ reveal_dir: null })}
                            />
                        </div>
                        <div className="gspb_row__col--6">
                            <SelectControl
                                value={reveal_dir}
                                options={[
                                    { label: __("Left to Right"), value: "lr" },
                                    { label: __("Right to Left"), value: "rl" },
                                    { label: __("Top to Bottom"), value: "tb" },
                                    { label: __("Bottom to top"), value: "bt" },
                                ]}
                                onChange={(value) => setAttributes({ reveal_dir: value })}
                            />
                        </div>
                    </div>

                    <BaseControl className="gs-label-row">
                        <NumberControl
                            onChange={(value) => setAttributes({ reveal_speed: parseFloat(value) })}
                            label={__('Speed')}
                            isDragEnabled
                            isShiftStepEnabled
                            shiftStep={1}
                            min={0.1}
                            max={100}
                            step={0.1}
                            value={reveal_speed}
                        />
                    </BaseControl>

                    <BaseControl className="gs-label-row">
                        <NumberControl
                            onChange={(value) => setAttributes({ reveal_delay: parseFloat(value) })}
                            label={__('Delay')}
                            isDragEnabled
                            isShiftStepEnabled
                            shiftStep={1}
                            min={0.1}
                            max={10}
                            step={0.1}
                            value={reveal_delay}
                        />
                    </BaseControl>

                    <div className="gspb_row">
                        <div className="gspb_row__col--9">
                            <span className="gspb_inspector_property-title">{__("Color")}</span>
                        </div>
                        <div className="gspb_row__col--3" style={{ textAlign: "right", justifyContent: "flex-end" }}>
                            <BlpgeColorPicker
                                color={reveal_bg}
                                onChange={(value) =>
                                    setAttributes({
                                        reveal_bg: value.rgb ? `rgba(${value.rgb.r}, ${value.rgb.g}, ${value.rgb.b}, ${value.rgb.a})` : value,
                                    })
                                }
                            />
                        </div>
                    </div>
                </>
            }
        </div>
    );
};

export default RevealForm;
